<script>
import {
  mapGetters,
  mapActions
} from "vuex";
import EmptyContent from "./EmptyViews/EmptyContent.vue";
import EmptyPreview from "./EmptyViews/EmptyPreview.vue";
export default {
  components: {
    EmptyContent,
    EmptyPreview,
  },
  methods: {
    ...mapActions({
      apimyborrow: "apimyborrow",
      ex_getborrowings: "store_borrowings/ex_getborrowings",
      ex_create: "store_borrowings/ex_createborrowing",
      ex_update: "store_borrowings/ex_updateborrowing",
      ex_delete: "store_borrowings/ex_deleteborrowing",
      ex_approveorreturn: "store_borrowings/ex_approveborrowing",
      ex_postnewmessage: "ex_postnewmessage",
      ex_getusers: "ex_getusers",
      apiitems: "store_items/apiitems",
    }),
    sendMessage() {
      let messagedata = {
        recipient: this.currentborrowing.api_borrower.api_id,
        subject: 'booking id : '+ this.currentborrowing.api_id,
        message: this.messageform,
      };
      this.ex_postnewmessage(messagedata).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Message",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
      });
    },
    tabAction(tab) {
      tab == 1 ? (this.settab = 1) : (this.settab = 2);
    },
    setactive(set) {
      this.paneservice = set;
    },

    print() {
      var toprint = document.getElementById("activecontent").innerHTML;
      var originalContents = document.body.innerHTML;
      document.body.innerHTML = toprint;
      window.print();
      document.body.innerHTML = originalContents;
    },
    gotoitem(getid) {
      let itemfetech = this.$router.resolve({
        name: "Itemdetails",
        params: {
          item: getid,
        },
      });
      window.open(itemfetech.href, "_blank");
    },
    setpreview(index) {
      this.previewreload = true;
      this.viewingitem = index;
      this.previewindex = index;
      setTimeout(() => {
        this.preview = true;
        this.currentborrowing = this.filterborrowing[index];
        this.previewreload = false;
      }, 1000);
    },
    setpendingpreview(borrowing) {
      this.previewreload = true;
      setTimeout(() => {
        this.preview = true;
        this.currentborrowing = borrowing;
        this.previewreload = false;
      }, 1000);
    },
    geturl(url) {
      let itemfetech = this.$router.resolve({
        name: "Itemdetails",
        params: {
          propscontent: url.api_id,
        },
      });
      window.open(itemfetech.href, "_blank");
    },
    formaction(type) {
      this.updatemode = type;
      switch (type) {
        case 1:
          this.getusers();
          break;
        case 2:
          this.formButton = "Update";
          this.updateform = JSON.parse(JSON.stringify(this.currentborrowing));
          break;
        case 3:
          this.formButton = "Approve";
          break;
        case 4:
          this.formButton = "Reject";
          break;
        case 5:
          this.formButton = "Return";
          break;
        case 6:
          this.formButton = "Delete";
          break;
      }
      this.$bvModal.show("modal-forms");
    },
    form_navigator() {
      switch (this.updatemode) {
        case 1:
          this.fn_create();
          break;
        case 2:
          this.preview = false;
          this.fn_update();
          break;
        case 3:
          this.fn_approveorreturn(1);
          break;
        case 4:
          this.fn_reject();
          break;
        case 5:
          this.fn_approveorreturn(2);
          break;
        case 6:
          this.fn_delete();
          break;
      }
    },
    // Create Borrowing
    fn_create() {
      this.ex_create(this.createform).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Create Borrowing",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        this.preview = false;
        this.defaultborrowing();
        this.getborrowings();
      });
    },
    // Update borrowing
    fn_update() {
      let data = {
        api_id: this.currentborrowing.api_id,
        api_data: this.updateform,
      };
      this.ex_update(data).then((response) => {

              this.reload = true;
        this.$bvToast.toast(response.message, {
          title: "Update Borrowing",
          variant: response.status ==  200 ? "primary" : "danger",
          solid: true,
        });
        if(response.status == 200){
               this.preview = false;
        this.getborrowings();

        }
      });
      this.reload = false;
    },
    // 3 Approve
    fn_approveorreturn(ex) {
      this.ex_approveorreturn({api_id : this.currentborrowing.api_id, action: ex}).then((response) => {
        this.$bvToast.toast(response.message, {
          title: ex == 1 ? "Approve" : "Return",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        this.preview = false;
        this.getborrowings();

      });
    },
        // 6 - Delete borrowing
    fn_reject() {
      this.ex_delete(this.currentborrowing.api_id).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Reject",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        this.preview = false;
        this.getborrowings();
      });
    },
    // 6 - Delete borrowing
    fn_delete() {
      this.ex_delete(this.currentborrowing.api_id).then((response) => {
        this.$bvToast.toast(response.message, {
          title: this.updatemode == 3 ? "Approve" : "Return",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        this.clearview();
      });
    },
    getusers() {
      this.ex_getusers().then((response) => {
        this.formButton = "Create";
        this.userscollections = response.data;
      });
      this.apiitems().then((response) => {
        this.itemscollections = response.data;
      });
    },
    getfaculty(university){
      return `${university.faculty+'@'+university.campus}`
    },
    getborrowings() {
      this.ex_getborrowings().then((response) => {
        this.apiborrowings = response.data;
        this.activeonly = this.apiborrowings.filter((borrowing) => {
          return (borrowing.api_request.status == 1)
        });
      });

    },
    clearview() {
      this.preview = false;
      this.viewingitem = "-";
      this.previewindex = "-",
      this.currentborrowing = {}
      this.settab = 1;
      this.filterinput = "";
      this.getborrowings();
    },
    thisbookingstatus() {
      switch (this.currentborrowing.api_request.status) {
        case 0:
          return "Pending";
        case 1:
          return "Approved";
        case 2:
          return "Completed";
        default:
          return "N/A";
      }
    },
    thisapprovedby() {
      return this.currentborrowing.api_request.approvedby ?
        this.currentborrowing.api_request.approvedby :
        "N/A";
    },
    customsearch() {
      let result = this.apiborrowings.filter((borrowing) => {
        return (borrowing.api_id == this.filterinput)
      });
      if (result.length > 0) {
        this.sample = result;
        this.pendingborrowing = result;
        this.$bvToast.toast('Borrowing found', {
          title: "Approve",
          variant: "primary",
          solid: true,
        });
      } else {
        this.searchempty = true;
      }
    },
    closepreview() {
      this.preview = false;
      this.viewingitem = "-";
      this.previewindex = "-",
      this.currentborrowing = {}
    },
    create_userpreview(id){
      this.userscollections.forEach((user)=>{
        if(user.api_id == id){
          this.displayselecteduser = user;
        }
      });
    },
        create_itempreview(id){
      this.itemscollections.forEach((item)=>{
        if(item.api_id == id){
          this.displayselecteditem = item;
        }
      });
    }
    ,defaultborrowing(){
      this.createform = {}
    this. displayselecteduser = {}
    }

  },
  data() {
    return {
      displayselecteduser: {},
      displayselecteditem: {},
      activeonly: [],
      usercode: "",
      searchempty: false,
      viewingitem: "-",
      currentPage: 1,
      perPage: 30,
      apiborrowings: [],
      currentborrowing: {},
      settab: 1,
      updatemode: 0,
      formButton: "",
      filterinput: "",
      reload: false,
      userscollections: [],
      itemscollections: [],
      preview: false,
      previewindex: "-",
      previewreload: false,
      query: false,
      messageform: "",
      borrowingform: {},
      createform: {

      },
      updateform: {},
      campus: [],
      search: "",
      borrowingcode: "",
      itemscollection: [],
      borroweditems: [],
      pendingborrowing: {},
      sample: {},
      variant: "dark",
      paneservice: 1,
      mainProps: {
        width: 50,
        height: 50,
        class: "m1"
      },
    };
  },
  computed: {
    ...mapGetters(["get_user"]),
    filterborrowing() {
      let all = this.activeonly.slice((this.currentPage - 1) * this.perPage, this.currentPage * this.perPage)
      return all.filter((borrowing) => {
        return (borrowing.api_borrower.api_name.toLowerCase().includes(this.filterinput.toLowerCase()) || borrowing.api_item.api_name.toLowerCase().includes(this.filterinput.toLowerCase()) || borrowing.api_borrowdate.includes(this.filterinput.toLowerCase()));
      })
    },
    watchtab() {
      return this.settab
    },
    checkreload(){
      return this.reload;
    }

  },
  watch: {
    watchtab() {
      this.preview = false;
      this.currentborrowing = {}
      this.filterinput = ""
      this.pendingborrowing = {}
    },
    checkreload(){
      this.preview = false;
      this.currentborrowing = {};
      this.pendingborrowing = {};
      this.filterinput = ""
    }


  },
  created() {
    this.getborrowings();
  },
};
</script>

