import Vue from 'vue'
import Vuex from 'vuex'
import axios from '@/axios'
import store_items from './items/index'
import store_borrowings from './borrowing/index'
import store_campuses from './campus/index'
import store_users from './users/index'
import store_shared from './shared/index'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    session: {
      token: localStorage.getItem('token') || null,
      active: false,
    },
    sessionUser: {
      },
    collection: {
      users: []
    }
  },
  mutations: {
    Loader: (state) => {
      state.store_shared.loading = !state.store_shared.loading
    },
    ListUsers: (state, collectiondata) => {
      state.collection.users = collectiondata.data
    },
    SetActiveUser: (state, data) => {
      state.sessionUser.name = data.api_firstname;
      state.sessionUser.picture = data.api_picture;
      state.sessionUser.role = data.api_role;
      state.sessionUser.userid = data.api_id;
      localStorage.setItem('active', true);
      localStorage.setItem('role', data.api_role);
      state.session.active = true;
    },
    SetNameOnly: (state, data) => {
      state.sessionUser.name = data;
    },
    SetPictureOnly: (state, data) => {
      state.sessionUser.picture = data;
    },
    EndSession: (state) => {
      state.session.active = false;
      state.sessionUser = {};
      localStorage.removeItem('token');
      state.sessionUser.name = "";
      state.sessionUser.picture = "";
      state.sessionUser.role = "";
      state.sessionUser.userid = "";
      state.session.active = false;
      localStorage.removeItem('active');
      localStorage.removeItem('role');
    },
  },
  actions: {
    ex_login({ commit, dispatch }, form) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.post("app_login", form).then((response => {
          console.log(response)
          commit('Loader')
          if (response.data.status == 401 || response.data.status == 403) {
            resolve(response.data);
          }
          else{
            localStorage.setItem('token', response.data.token)
            resolve(dispatch("exsessiondata", response.data.token));
          }
        })).catch(err => {
          commit('Loader')
          reject(err)
        });
      })
    },

    // Register new user
    ex_register({ commit }, userform) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.post("app_register", userform).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err.message)
        })
      })
    },
        //VALIDATE USER
    async exsessiondata({ commit }, token) {
      commit('Loader')
      try{
          let response = await axios.get("app_userinfo",
          {
            headers: {
              'Authorization': 'Bearer ' + token,
            }
          })
        commit('SetActiveUser', response.data)
        commit('Loader');
        return response;
      }
      catch(e){
        commit('Loader')
        commit('EndSession')
        console.log(e)
      }
    },

    //LOG OUT USER
    exlogout({ commit }) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.get("app_logout",
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }
        ).then((response => {
          commit('EndSession')
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },

    //USER CHANGE PASSWORD - Done
    ex_changepassword({ commit }, password) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.post("app_changepassword",
          password,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }
        ).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },

    //UPDATE PROFILE - Done
    ex_imaging({ commit }, imagedata) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.post("app_imaging", imagedata,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }
        ).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },

    //USER REQUEST RESET PASSWORD
    ex_resetpassword({ commit }, useremail) {
      console.log("Check Reset Password");
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.post("app_forgot", { email: useremail },   {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }
        ).then((response => {
          commit('Loader')
          console.log(response.data);
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },
    //Get Own Profile  - Done
    ex_profile({ commit }) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.get("app_users/"+ 0,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }
        ).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },

    //Validate Password - Done
    ex_validatepassword({ commit}, validate) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.post("app_checkpassword", { password: validate},   {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err.message)
        })
      })
    },

    //Remove Account  - Done
    ex_unregister({ commit }) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.get("app_closeaccount",   {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }).then((response => {
          commit('Loader')
          if (response.data.status == 200) {
            commit('EndSession')
          }
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },

//
    apimyborrow({ commit }) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.get("app_borrow",   {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }).then((response => {
          commit('Loader')
          resolve(response.data.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },

    //GET ALL USERS
    ex_getusers({ commit }) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.get("app_users",   {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }).then((response => {
          commit('ListUsers', response.data)
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },

    //UPDATE USER PROFILE
    ex_apiupdateuser({ commit }, updatedata) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.post("app_users/" + updatedata.get('api_id'), updatedata,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }
        ).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },

        //UPDATE USER PROFILE
    ex_apiprofileupdate({ commit }, updatedata) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.post("app_updateuserprofile", updatedata
        ,   {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }).then((response => {
          console.log(response.data);
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },
    

    //BLOCK UNBLOCK USER
    ex_userBlock({ commit, dispatch }, blockuser) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.put('app_blockusers/' + blockuser, blockuser,   {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }).then((response => {
          dispatch('ex_getusers')
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },

    //BLOCK UNBLOCK USER
    ex_userDelete({ commit }, deleteuser) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.delete('app_users/' + deleteuser,   {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },

    apimessage({ commit }) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.get("app_messages",
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }
        ).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },

    apiamessage({ commit }, content) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.get("app_messages/" + content,   {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },

    ex_closethread({ commit }, thread) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.post("app_messageclose", thread,   {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },
    // SEND EMAIL
    apisendmessage({ commit }, messagecontent) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.post("app_messages",
          messagecontent
        ,   {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },  // SEND EMAIL
    ex_postnewmessage({ commit }, messagecontent) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.post("app_messagenew",
          messagecontent,   {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            }
          }
        ).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },

    // GET FEEDS - Done
    ex_getfeeds({ commit }) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.get("app_feeds",{
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            },
          }).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },
    // DELETE FEEDS - Done
    ex_feeddelete({ commit }, deletefeed) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.delete('app_feeds/' + deletefeed,{
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            },
          }).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },
    // HIDE FEEDS - Done
    ex_feedhide({ commit }, feed) {
      commit('Loader')
      return new Promise((resolve, reject) => {
        axios.put("app_feeds/" + feed, feed,{
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
            },
          }
        ).then((response => {
          commit('Loader')
          resolve(response.data)
        })).catch(err => {
          commit('Loader')
          reject(err)
        })
      })
    },   
    // Update Name
    ex_updatecurrentname({ commit }, namedata) {
      commit('Loader')
      commit('SetNameOnly', namedata.api_firstname)
      commit('Loader')
    },
        // Update Name
      ex_updatecurrentpicture({ commit }, picturedata) {
      commit('Loader')
      commit('SetPictureOnly', picturedata.api_picture)
      commit('Loader')
    },
    // HIDE FEEDS - Done
    checkstatus() {
      return localStorage.getItem('active') || false;
    },
    
  },
  modules: {
    store_items,
    store_borrowings,
    store_campuses,
    store_users,
    store_shared,
  },
  getters: {
    get_active: (state) => {
      return state.session.sessionUser
    },
    get_user: (state) => {
      return state.sessionUser;
    },
    get_usersession: (state) => {
      return state.session
    },
    get_loader: (state) => {
      return state.store_shared.loading
    },
    get_apiusers: (state) => {
      return state.collection.users
    },
    get_localactive: () => {
      return localStorage.getItem('active') || false;
    },
    get_userrole: () => {
      return localStorage.getItem('role') || false;
    },
  }
})
