            Cancel
          </b-button>
          <b-button class="float-right" variant="danger" @click="ok()">
            Continue
          </b-button>
        </div>
      </template>
    </b-modal>
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import EmptyContent from "./EmptyViews/EmptyContent.vue";
import EmptyPreview from "./EmptyViews/EmptyPreview";

export default {
  name: "app_campusescollection",
  components: {
    EmptyContent,
    EmptyPreview,
  },
  data() {
    return {
      apiuserscollection: [],
      //CAMPUS
      apicampusescollection: [],
      apifacultycollection: [],
            imagepreviewer: "",
            file: "",
      setmodal: 0,
      updatemode: 0,
      confirmation: false,
      selectedcampus: 0,
      rows: 100,
      perPage: 1,
      viewingitem: "",
      currentPage: 1,
      settab: 1,
      preview: false,
      previewindex: "-",
      tabIndex: 0,
      facultyedit: "false",
      addfacultyform: {
        api_id: "",
        api_thumbnail: [],
      },
      facultyform: {
        api_name: "",
        api_description: "",
      },
      currentview: {},
      campusform: {
        api_name: "",
        api_thumbnail: [],
        api_description: "",
        api_address: "",
        api_email: "",
      },

      campuscreateform: {
        api_name: "",
        api_thumbnail: [],
        api_description: "",
        api_address:
          "",
        api_contact: "",
        api_email: "",
      },

      selected: "A",
      show_updatemodal: false,
      show_createcampus: false,
      show_deletecampus: false,
      previewreload: false,
      search: "",
      filterinput: "",
      pendingcampus: [],
      variant: "dark",
      role: [
        { value: 1, text: "Student" },
        { value: 2, text: "Staff" },
        { value: 3, text: "Admin" },
      ],
      campus: [],
      faculty: [],
      campusfaculties: [],
      formButton: "",

      //USER
          createform: {
        api_firstname: "",
        api_othernames: "",
        api_role: 1,
        api_picture: [],
        api_email: "",
        api_nim: "",
        api_contact: "",
        api_password: "",
        api_cpassword: "",
        api_campus: "",
        api_faculty: "",
        api_staffid: 0,
        api_status: null,
      },
                    update_picture: [],
            passwordvalue: "",
      passwordchoice: [{
          item: "1",
          name: "Current"
        },
        {
          item: "2",
          name: "New Password"
        },
        {
          item: "3",
          name: "Generate Password"
        },
      ],
      passwordchoicecreate: [{
          item: "1",
          name: "New Password"
        },
        {
          item: "2",
          name: "Generate Password"
        },
      ],
            passwordSelector: 1,
      showpassword: false,
      activecount: 0,
      blockedcount : 0

    };
  },
  methods: {
    ...mapActions({
      apicampuses: "store_campuses/apicampuses",
      ex_campuses: "store_campuses/ex_campuses",
      ex_campusupdate: "store_campuses/ex_campusupdate",
      ex_querycampusfaculty: "store_campuses/ex_querycampusfaculty",
      ex_addcampusfaculty: "store_campuses/ex_addcampusfaculty",
      ex_faculties: "store_campuses/ex_faculties",
      ex_putfaculty: "store_campuses/ex_putfaculty",
      ex_getfaculties: "store_campuses/ex_getfaculties",
      ex_unmapfaculty: "store_campuses/ex_unmapfaculty",
      ex_deletecampus: "store_campuses/ex_deletecampus",
      ex_deletefaculty: "store_campuses/ex_deletefaculty",

      apiitems: "apiitems",
      apimyborrow: "apimyborrow",
      apiusers: "apiusers",
      ex_apiupdateuser: "ex_apiupdateuser",
      ex_userBlock: "ex_userBlock",
      ex_userDelete: "ex_userDelete",
      ex_campusfaculties: "store_campuses/ex_campusfaculties",
      ex_getusers: "ex_getusers",
      ex_register: "ex_register"
    }),
    confirm_modelAction(bvModalEvt) {
      bvModalEvt.preventDefault();
      this.modalConfirmAction();
    },
    modalConfirmAction() {
      this.confirmation
        ? this.$nextTick(() => {
            this.$bvModal.hide("modal-confirmation");
            this.$bvModal.show("modal-forms");
            this.confirmation = false;
          })
        : this.$bvModal.show("modal-forms");
    },
    onConfirmCancel() {
      this.confirmation = false;
    },
    // Preview Campus
    setpreview(index) {
      this.previewreload = true;
      setTimeout(() => {
        this.preview = true;
        this.previewindex = index;
        this.currentview = JSON.parse(JSON.stringify(this.filterusers[index]))
        this.previewreload = false;
      }, 500);
    },
    // Forms
    formaction(type) {
      this.updatemode = type;
      switch (type) {
        case 1:
          // Create campus
          this.formButton = "Create";
          break;
        case 2:
          // Update campus
          this.formButton = "Update";
            this.updateform = JSON.parse(JSON.stringify(this.currentview));
          break;
        case 3:
          // Delete campus
          this.formButton = this.currentview.api_status == 1 ? "Block" : "Unblock";
          this.confirmation = true;
          break;
        case 4:
          // Delete faculty
          this.formButton = "Delete";
          this.confirmation = true;
          break;
      }
            if (type == 1 || type == 2) {
        this.fn_getcampuses();
      }
      this.confirmation
        ? this.$bvModal.show("modal-confirmation")
        : this.$bvModal.show("modal-forms");
    },
    geturl(url) {
      let itemfetech = this.$router.resolve({
        name: "Itemdetails",
        params: {
          item: url,
        },
      });
      window.open(itemfetech.href, "_blank");
    },
    countcampus() {
      return this.apiuserscollection.length;
    },
    countfaculty() {
      return this.apifacultycollection.length;
    },
    fn_cancelupdate() {
      this.show_updatemodal = false;
      this.form = {};
    },
    form_navigator() {
      switch (this.updatemode) {
        case 1:
          this.fn_usercreate();
          // this.fn_addcampus();
          break;
        case 2:
          this.fn_userupdate();
          break;
        case 3:
          this.fn_userBlock();
          break;
        case 4:
          this.fn_deleteuser();
          break;
      }
    },

    // 1 - Create User
    fn_usercreate() {
      this.createform.api_cpassword = this.createform.api_password;    
      let data = new FormData();
      data.append("api_firstname", this.createform.api_firstname);
      data.append("api_othernames", this.createform.api_othernames);
      data.append("api_role", this.createform.api_role);
      data.append("api_picture", this.file);
      data.append("api_email", this.createform.api_email);
      data.append("api_nim", this.createform.api_nim);
      data.append("api_password", this.createform.api_password);
      data.append("api_cpassword", this.createform.api_cpassword);
      data.append("api_contact", this.createform.api_contact);
      data.append("api_campus", this.createform.api_campus);
      data.append("api_faculty", this.createform.api_faculty);
      data.append("api_staffid", this.createform.api_staffid);
      data.append("api_status", this.createform.api_status);

      this.ex_register(data).then((response) => {
        if (response.status == 200) {
            this.$bvToast.toast(response.message, {
              title: "Register",
              variant: response.status == 200 ? "primary" : "danger",
              solid: true,
            });
          
        this.defaultcreateform();
        this.getusers();
        }
      });
    },




    // 2 - update
    fn_userupdate() {
      if(this.passwordSelector != 1){
        if(this.passwordSelector == 3){
          this.updateform.api_password = this.passwordvalue;
        }
        if(!this.updateform.api_password || this.updateform.api_password.length < 6){
        alert("Check password length ");
        this.passwordSelector = 1;
        return;
        }
      }
      let data = new FormData();
      data.append("_method", "PUT");
      data.append("api_id", this.updateform.api_id);
      data.append("api_firstname", this.updateform.api_firstname);
      data.append("api_othernames", this.updateform.api_othernames);
      data.append("api_role", this.updateform.api_role);
      data.append("api_picture", this.newPicture());
      data.append("api_email", this.updateform.api_email);
      data.append("api_nim", this.updateform.api_nim);
      data.append("api_password", this.updateform.api_password);
      data.append("api_cpassword", this.updateform.api_password);
      data.append("api_contact", this.updateform.api_contact);
      data.append("api_role", this.updateform.api_role);
      data.append("api_campus", this.updateform.api_campus);
      data.append("api_faculty", this.updateform.api_faculty);
      data.append("api_staffid", this.updateform.api_staffid);
      data.append("api_status", this.updateform.api_status);

      this.ex_apiupdateuser(data).then((response) => {
          this.$bvToast.toast(response.message, {
            title: "Update",
            variant: response.status == 200 ? "primary" : "danger",
            solid: true,
          });
        if (response.status == 200) {
          this.preview = false;
          this.$nextTick(() => {
            this.$bvModal.hide("modal-forms");
          });
          this.clearview();
        }

          if(response.status == 400){
            this.updateerror = response.data
          }
      });
    
    },



    //5 - Unmap a faculty
    fn_userBlock() {
  this.ex_userBlock(this.currentview.api_id).then((response) => {
        this.$bvToast.toast(response.message, {
          title: this.currentview.api_status ? "Block User " : "Unblock User",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        if (response.status == 200) {
          this.preview = false;
          this.clearview();
        }
      })
    },


    
    // 4 - Delete user
    fn_deleteuser() {
      this.ex_userDelete(this.currentview.api_id).then((response) => {
                this.$bvToast.toast(response.message, {
          title: "Delete User",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        if (response.status == 200) {
          this.preview = false;
          this.clearview();
        }
      });
    },

    get_campusheader() {
      return this.currentview.api_role == 1 ? "NIM" : "Staff ID";
    },
    get_campusidentity() {
      return this.currentview.api_role == 1
        ? this.currentview.api_nim
        : this.currentview.api_staffid;
    },
    generatepassword() {
      let password =
        Math.random().toString(36).substring(2, 6) +
        Math.random().toString(36).substring(2, 8);
      this.passwordvalue = password;
      this.form.api_password = password;
    },
    fn_showcreatecampus() {
      this.show_createcampus = true;
    },
    abbreviate(name) {
      var matches = name.match(/\b(\w)/g);
      var acronym = matches.join("");
      return acronym;
    },

    // Copy Faculty to Form

    // Edit and Update Faculty name and description
    updatefaculty() {
      let data = {
        faculty: this.currentview.api_id,
        api_name: this.facultyform.api_name,
        api_description: this.facultyform.api_description,
      };
      this.ex_putfaculty(data).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Update Faculty",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
      });
      this.clearFacultyView();
    },
    clearview() {
      this.getusers();
    },
    clearFacultyView() {
      this.preview = 0;
      this.currentview = {};
      this.getfaculties();
      this.settab = 2;
    },
    customsearch() {},
    closepreview() {
      this.preview = false;
      this.viewingitem = "-";
      this.previewindex = "-"
    },
      uploadImage(e) {
      const file = e.target.files[0];
      this.file = e.target.files[0];
      this.imagepreviewer = URL.createObjectURL(file);
      URL.revokeObjectURL(file); // free memory
    },
        clearPicture() {
      this.campuscreateform.api_thumbnail = [];
      this.imagepreviewer = "";
      this.file = ""
    },
    setThumbnail(){
      return this.file ? this.file : null
    },



    // USERS
       getusers() {
      this.ex_getusers().then((response) => {
        this.apiuserscollection = response.data;
        this.countblockedusers();
      });
    },
        getcampusfaculty(user) {
      return user.api_faculty.api_name + " @ " + user.api_campus.api_name;
    },
      getrole(role) {
      return role == 1 ? "Student" : role == 2 ? " Staff " : " Admin ";
    },
        getfullname(name) {
      return name.api_firstname + " " + name.api_othernames;
    },
        get_userheader() {
      return this.currentview.api_role == 1 ? "NIM" : "Staff ID";
    },
    get_useridentity() {
      return this.currentview.api_role == 1 ?
        this.currentview.api_nim :
        this.currentview.api_staffid;
    },
    fn_getcampuses() {
      this.ex_campusfaculties().then((response) => {
        this.campus = response.data.filter((campus) => {
          return campus.api_name != 'admin';
        });
       this.campus.filter((campus) => {
          if (this.updatemode == 2 && this.currentview.api_campus.api_id == campus.api_id) {
          this.updateform = JSON.parse(JSON.stringify(this.currentview));
          this.updateform.api_campus = this.currentview.api_campus.api_id;
          this.faculty = campus.api_faculties;
          this.updateform.api_faculty = this.currentview.api_campusinfo
          this.updateform.api_picture = [];
          }
        });
      });
    },
    getUserfaculty() {
      this.campus.forEach((campus) => {
        if(this.selectedcampus == campus.api_id){
          this.faculty = campus.api_faculties;
        }
      });
    }, 
        passwordtype(e){
      this.passwordSelector = e;
    },
        newPicture(){
      return this.file ? this.file : this.updateform.api_picture;
    },
        countblockedusers() {
      let active = 0;
      let blocked = 0;
      this.apiuserscollection.filter((user) => {
        if (user.api_status == 0) {
          blocked+= 1;
        }
        if (user.api_status == 1) {
          active += 1;
        }
      });
      this.activecount = active;
      this.blockedcount = blocked;
    },
      defaultcreateform(){
         this.imagepreviewer = "";
        this.createform.api_firstname = "";
        this.createform.api_othernames = "";
        this.createform.api_role = 1;
        this.createform.api_picture = [];
        this.createform.api_email ="";
        this.createform.api_nim = "";
        this.createform.api_contact = "";
        this.createform.api_password = "";
        this.createform.api_cpassword ="";
        this.createform.api_campus = "";
        this.createform.api_faculty = "";
        this.createform.api_staffid = 0;
        this.createform.api_status = null;
      },
      campusvalue(value){
        this.selectedcampus = value;
      }
  },
  computed: {
    ...mapGetters(["get_user"]),
      totalresult() {
      let counter = this.items.length.toString();
      return `(${counter})  ${
        this.items.length == 1 ? " item " : "items "
      }  found`;
    },
    activeitem() {
      return this.borroweditems.length > 0 ? 1 : 0;
    },
    searchtype() {
      return this.settab;
    },

    checkpassword() {
      return this.passwordSelector;
    },

    closeaction() {
      return this.preview;
    },
    getcounter() {
      return this.apiuserscollection;
    },
      watchtab() {
      return this.settab
    },
        watchpreview() {
      return this.preview;
    },

  
    filtercampuses() {
      return this.apicampusescollection.filter((campus) => {
        return campus.api_name
          .toLowerCase()
          .includes(this.filterinput.toLowerCase());
      });
    },
    filterfaculties() {
      return this.apifacultycollection
        ? this.apifacultycollection.filter((faculty) => {
            return faculty.api_name
              .toLowerCase()
              .includes(this.filterinput.toLowerCase());
          })
        : null;
    },

    whichmodal() {
      return this.setmodal;
    },
    previewer() {
      return this.preview;
    },

    //USER
        filterusers() {
      return this.apiuserscollection.filter((user) => {
        if (this.settab == 1) {
          return (
            user.api_status == 1 &&
            user.api_firstname
            .toLowerCase()
            .includes(this.filterinput.toLowerCase())
          );
        }
        return (
          !user.api_status &&
          user.api_firstname.toLowerCase().includes(this.filterinput.toLowerCase())
        );
      });
    },
   activecampus() {
     return this.selectedcampus;
    },
  },
  watch: {
    searchtype() {
      this.previewreload = true;
      setTimeout(() => {
        this.previewreload = false;
        this.preview = false;
      }, 1000);
    },
    whichmodal() {
      this.show_createcampus = true;
    },
    activecampus() {
      this.getUserfaculty();
    },
  },
  created() {
     this.getusers();
  },
};
</script>

