<template>
<b-container fluid>
  <div class="main-wrapper">
    <b-container >
          <div class="message-body">

        <div class="message-center">
          <h5 class="css-indicator">
            <b-icon icon="envelope"></b-icon>Messages
          </h5>
<!--           <b-button variant="outline-dark" v-if="get_user.role != 1">
            <b-icon icon="inbox-fill"></b-icon> Write Message
          </b-button> -->
        </div>

        <div class="message-wrapper">
          <template v-if="messagecollection.length > 0">
            <b-list-group>
              <b-list-group-item
                @click="getthread(message)"
                v-for="message in messagecollection"
                :key="message.api_id"
                class="d-flex justify-content-between align-items-start"
              >
                <div class="css-messagecontent">
                  <h5>Subject : {{ message.api_subject }}</h5>
                  <p>
                    {{
                      message.api_message[message.api_message.length - 1]
                        .content
                    }}
                  </p>
                </div>
                <div class="css-messagestatus">
                    <b-iconstack font-scale="2">
                      <b-icon
                        stacked
                        icon="circle-fill"
                        variant="light"
                      ></b-icon>
                      <b-icon
                        stacked
                        :icon="messagestatus(message.api_message) ? 'envelope-open' : 'envelope-fill'"
                        scale="0.5"
                        variant="secondary"
                      ></b-icon>
                    </b-iconstack>
                </div>
              </b-list-group-item>
            </b-list-group>
          </template>
          <template v-else>
            <div class="no-message">YOU HAVE NO MESSAGE!</div>
          </template>
        </div>
            </div>

  </b-container>
  </div>
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "app-message",
  data: () => ({
    mainProps: { width: 30, height: 30, class: "m1" },
    messagecollection: [],
    fields: ["no", "sender", "subject", "status"],
  }),
  methods: {
    ...mapActions(["apimessage"]),
    getmessages() {
      this.apimessage().then((response) => {
        this.messagecollection = response.data;
      });
    },
    messagestatus(messagelog) {
      let read = false;
      messagelog.forEach((element) => {
        if (element.read === 1) {
          read = true;
        }
      });
      return read;
    },
    getthread(item) {
      this.$router.push({
        name: "MessageContent",
        params: {
          propscontent: item.api_id,
        },
      });
    },
  },
  computed: {
    ...mapGetters(["get_user"]),
  },
  created() {
    this.getmessages();
  },
};
</script>

