<template>
  <div class="app-base">
    <template v-if="apifeedscollection.length > 0">
      <b-list-group >
        <b-list-group-item
          class="flex-column align-items-start feedlist"
          v-for="(feed, fin) in apifeedscollection" :key="fin"
          :class="{ active: activeindex == fin }"
          @mouseenter="activeindex == fin"
        >
          <div class="counter">
            <b-badge variant="primary" pill>{{ fin + 1 }}</b-badge>
          </div>
          <div class="d-flex w-100 justify-content-between" @click="feedcontent(feed)">
            <b-img :src="feed.api_feed.api_thumbnail" fluid></b-img>
            <div>
              <div class="d-flex w-100 justify-content-between">
              <h5 class="mb-1" style="text-transform: capitalize">{{ feed.api_feed.api_name }}</h5>
              <small>{{ feed.api_time }}</small>
            </div> 
              <p class="mb-1 formatlength">
                {{getlength(feed.api_feed.api_description)}}
              </p>
            </div>
   
          </div>
          <div class="xfeed" v-if="get_user.role == 3">
            <div class="mt-3">
              <b-button-group size="sm">
                <b-button
                  pill
                  size="sm"
                  class="mb-2"
                  :variant="feed.api_status ? 'primary' : 'secondary'"
                  @click="fn_hide(feed)"
                >
                  <b-icon
                    :icon="feed.api_status ? 'eye-slash' : 'eye'"
                    aria-hidden="true"
                  ></b-icon>
                  {{ feed.api_status ? "Hide" : "Unhide" }}
                </b-button>
                <b-button pill size="sm" class="mb-2" variant="light" disabled>
                </b-button>
                <b-button
                  pill
                  size="sm"
                  class="mb-2"
                  variant="danger"
                  @click="fn_delete(feed)"
                >
                  <b-icon icon="trash" aria-hidden="true"></b-icon> Delte
                </b-button>
              </b-button-group>
            </div>
          </div>
        </b-list-group-item>
      </b-list-group>

    </template>
    <template v-else>
      <empty-preview></empty-preview>
    </template>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import EmptyPreview from "./EmptyViews/EmptyPreview.vue";

export default {
  name: "app-feedscollection",
  components: {
    EmptyPreview,
  },
  data() {
    return {
      activeindex: null,
      apifeedscollection: [],
    };
  },
  methods: {
    ...mapActions(["ex_getfeeds", "ex_feeddelete", "ex_feedhide"]),
    feedcontent(feed) {
      let getfeeds = this.$router.resolve({
        name: feed.api_type == 1 ? "Itemdetails" : "MessageContent",
        params: {
          propscontent: feed.api_feed.api_id,
        },
      });
      window.open(getfeeds.href, "_blank");
    },
    fn_getfeeds() {
      this.ex_getfeeds().then((response) => {
        this.apifeedscollection = response.data;
      });
    },
    fn_delete(feed) {
      this.ex_feeddelete(feed.api_id).then((response) => {
              this.fn_getfeeds();
        this.$bvToast.toast(response.message, {
          title: "Delete Feed",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
      });
    },
    fn_hide(feed) {
      this.ex_feedhide(feed.api_id).then((response) => {
              this.fn_getfeeds();
        this.$bvToast.toast(response.message, {
          title: feed.api_status ? "Hide Feed" : "Unhide Feed",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
      });
    },
    getlength(description){
      return description && description.length > 200 ? description.slice(0, 196) + '...' : description
    }
  },
  computed: {
    ...mapGetters(["get_user"]),
  },
  created() {
    this.fn_getfeeds();
  },
};
</script>

