<script>
import { mapActions, mapGetters } from "vuex";
// import EmptyContent from "../Collections/EmptyViews/EmptyContent.vue";

export default {
  components: {},
  name: "item-details",
  props: ["propscontent"],
  comments: {},
  data() {
    return {
      unavailable: false,
      granted: false,
      itemdata: {},
      isconfirmed: {
        api_profile: "",
        api_campus: null,
      },
      notconfirmed: "",
      form: {
        api_item: this.propscontent,
        api_borrowdate: "",
        api_returndate: "",
        api_reason: "",
        api_borrower: "self",
      },
      slide: 0,
      sliding: null,
      itemcontent: {},
      fields: ["photo", "name", "description", "actions"],
      activeservice: 1,
      variant: "dark",
    };
  },
  methods: {
    ...mapActions({
      apigetitem: "store_items/apigetitem",
      apiborrow: "store_borrowings/apiborrow",
    }),
    tobook() {
      this.$router.push({
        name: "Booking",
        params: {
          propscontent: this.propscontent,
        },
      });
    },
    onSlideStart(slide) {
      let clear = {};
      clear["a"] = slide;
      delete clear["a"];

      this.sliding = true;
    },
    onSlideEnd(slide) {
      let clear = {};
      clear["a"] = slide;
      delete clear["a"];

      this.sliding = false;
    },

    finalaction() {
      // this.$bvModal.msgBoxOk("Booking Successful").then((value) => {
      //   alert(value);
      // });
    },
    getitem() {
      this.apigetitem(this.propscontent).then((response) => {
        if (!status) {
          this.unavailable = true;
        }
        this.itemcontent = response.data;
      });
    },
    onSubmit() {},
    getfaculty(item) {
      return `${item.api_campus.api_name}, ${item.api_faculty.api_name}`;
    },
    processborrow(bvModalEvt) {
      bvModalEvt.preventDefault();
      this.apiborrow(this.form).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Borrowing",
          variant: response.status == 200 ? "success" : "danger",
          solid: true,
        });
        if (response.status == 200 || response.status == 202) {
          this.granted = true;
          this.isconfirmed = response.data;
          this.$nextTick(() => {
            this.$bvModal.hide("modal-xl");
          });
        }
        return (this.notconfirmed = response.message);
      });

    },
    fn_userupdate(bvModalEvt) {
      bvModalEvt.preventDefault();
      this.$nextTick(() => {
        this.$bvModal.hide("modal-multi-2");
      });
    },
        videoplayer(link){
      return "https://www.youtube.com/embed/" + link;
    }
  },

  computed: {
    ...mapGetters(["get_usersession"]),
    confirmedheader() {
      return this.isconfirmed ? "Successful" : "Error";
    },
  },
  created() {
    this.getitem();
  },
};
</script>

