            disabled
            :placeholder="'Bookinig ID : ' + currentview.api_id.toString()"
          ></b-form-input>
      </b-form-group>

      <b-form-group label="Message" label-for="name-input" invalid-feedback="Name is required">
        <b-form-textarea id="textarea" v-model="messageform" placeholder="Enter something..." rows="9" max-rows="12"></b-form-textarea>
      </b-form-group>
