<script>
import { mapGetters, mapActions } from "vuex";
import EmptyContent from "./EmptyViews/EmptyContent.vue";
import EmptyPreview from "./EmptyViews/EmptyPreview";

export default {
  name: "app_campusescollection",
  components: {
    EmptyContent,
    EmptyPreview,
  },
  data() {
    return {
      apiborrowings: [],
      allborrowing: [],
      currentborrowing: {},
      userscollections: [],
      itemscollections: [],
      displayselecteduser: {},
      displayselecteditem: {},
      usercode: "",
      searchempty: false,
     messageform: "",
      borrowingform: {},
      createform: {

      },
      updateform: {},

      pendingborrowing: {},





      apicampusescollection: [],
      apifacultycollection: [],
            imagepreviewer: "",
            file: "",
      setmodal: 0,
      updatemode: 0,
      confirmation: false,
      rows: 100,
      perPage: 1,
      viewingitem: "",
      currentPage: 1,
      settab: 0,
      preview: false,
      previewindex: "-",
      tabIndex: 0,
      facultyedit: "false",
      addfacultyform: {
        api_id: "",
        api_thumbnail: [],
      },
      facultyform: {
        api_name: "",
        api_description: "",
      },
      currentview: {},

      campusform: {
        api_name: "",
        api_thumbnail: [],
        api_description: "",
        api_address: "",
        api_email: "",
      },

      campuscreateform: {
        api_name: "",
        api_thumbnail: [],
        api_description: "",
        api_address:
          "",
        api_contact: "",
        api_email: "",
      },

      passwordvalue: "",
      selected: "A",
      show_updatemodal: false,
      show_createcampus: false,
      show_deletecampus: false,
      previewreload: false,
      search: "",
      filterinput: "",
      pendingcampus: [],
      variant: "dark",
      role: [
        { value: 1, text: "Student" },
        { value: 2, text: "Staff" },
        { value: 3, text: "Admin" },
      ],
      campus: [],
      faculty: [],
      campusfaculties: [],
      formButton: "",
            activeonly: [],

    };
  },
  methods: {
    ...mapActions({
      ex_campusfaculties: "store_campuses/ex_campusfaculties",
      apicampuses: "store_campuses/apicampuses",
      ex_campuses: "store_campuses/ex_campuses",
      ex_campusupdate: "store_campuses/ex_campusupdate",
      ex_querycampusfaculty: "store_campuses/ex_querycampusfaculty",
      ex_addcampusfaculty: "store_campuses/ex_addcampusfaculty",
      ex_faculties: "store_campuses/ex_faculties",
      ex_putfaculty: "store_campuses/ex_putfaculty",
      ex_getfaculties: "store_campuses/ex_getfaculties",
      ex_unmapfaculty: "store_campuses/ex_unmapfaculty",
      ex_deletecampus: "store_campuses/ex_deletecampus",
      ex_deletefaculty: "store_campuses/ex_deletefaculty",


        apimyborrow: "apimyborrow",
      ex_getborrowings: "store_borrowings/ex_getborrowings",
      ex_create: "store_borrowings/ex_createborrowing",
      ex_update: "store_borrowings/ex_updateborrowing",
      ex_delete: "store_borrowings/ex_deleteborrowing",
      ex_approveorreturn: "store_borrowings/ex_approveborrowing",
      ex_postnewmessage: "ex_postnewmessage",
      ex_getusers: "ex_getusers",
      apiitems: "store_items/apiitems",


    }),
    tabAction(tab) {
      tab == 1 ? this.getborrowings() : this.getfaculties();
      this.settab = tab;

    },
    confirm_modelAction(bvModalEvt) {
      bvModalEvt.preventDefault();
      this.modalConfirmAction();
    },
    modalConfirmAction() {
      this.confirmation
        ? this.$nextTick(() => {
            this.$bvModal.hide("modal-confirmation");
            this.$bvModal.show("modal-forms");
            this.confirmation = false;
          })
        : this.$bvModal.show("modal-forms");
    },
    onConfirmCancel() {
      this.confirmation = false;
      this.defaultborrowing();
    },
    // Get campuses...
    getcampuses() {
      this.ex_campusfaculties().then((response) => {
                this.apicampusescollection = response.data.filter(function(item) {
    return item.api_id !== 20201205125211
})
        this.settab = 1;
      });
    },
    // Get Faculties
    getfaculties() {
      this.ex_getfaculties().then((response) => {
        this.apifacultycollection = response.data.filter((faculty)=>{
          return faculty.api_name != 'admin';
        })
      });
      this.settab = 2;
    },
    // Preview Campus
    setpreview(index) {
      this.previewreload = true;
      setTimeout(() => {
        this.preview = true;
        this.previewindex = index;
        this.currentview = JSON.parse(JSON.stringify(this.filterborrowing[index]))

        this.previewreload = false;
      }, 500);
    },
        setpendingpreview(borrowing) {
      this.previewreload = true;
      setTimeout(() => {
        this.preview = true;
        this.currentview = borrowing;
        this.previewreload = false;
      }, 1000);
    },
    // Forms
    formaction(type) {
      this.updatemode = type;
      switch (type) {
        case 1:
          // Create campus
          this.getusers();
          break;       
        case 2:
          // Update campus
          this.updateform = JSON.parse(JSON.stringify(this.currentview));
          this.formButton = "Update";
          break;
       case 3:
          this.formButton = "Approve";
          break;
        case 4:
          this.formButton = "Reject";
          break;
        case 5:
          this.formButton = "Return";
          break;
        case 6:
          this.formButton = "Delete";
          break;
      }
      this.confirmation
        ? this.$bvModal.show("modal-confirmation")
        : this.$bvModal.show("modal-forms");
    },
    geturl(url) {
      let itemfetech = this.$router.resolve({
        name: "Itemdetails",
        params: {
          propscontent: url.api_id,
        },
      });
      window.open(itemfetech.href, "_blank");
    },
 
    countfaculty() {
      return this.apifacultycollection.length;
    },
    fn_cancelupdate() {
      this.show_updatemodal = false;
      this.form = {};
    },
    form_navigator() {
      switch (this.updatemode) {
        case 1:
          this.fn_create();
          break;
        case 2:
          this.fn_update();
          break;
        case 3:
          this.fn_approveorreturn(1);
          break;
        case 4:
          this.fn_reject();
          break;
        case 5:
          this.fn_approveorreturn(2);
          break;
        case 6:
          this.fn_delete();
          break;
      }
    },

    // 1 - Add a campus
    fn_create() {
      this.ex_create(this.createform).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Create Borrowing",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        if(response.status == 200){
          this.defaultborrowing();
          this.clearview();
        }
      });
    },
    // 2 - update
    fn_update() {
      let data = {
        api_id: this.currentview.api_id,
        api_data: this.updateform,
      };

      this.ex_update(data).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Update Borrowing",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        if (response.status == 200) {
          this.clearview();
        }
      });
    },

    // 3 Approve
    fn_approveorreturn(ex) {
      this.ex_approveorreturn({api_id : this.currentview.api_id, action: ex}).then((response) => {
        this.$bvToast.toast(response.message, {
          title: ex == 1 ? "Approve" : "Return",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        if(response.status == 200){
          this.clearview();
        }
      });
    },

        // 6 - Reject
    fn_reject() {
      this.ex_delete(this.currentview.api_id).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Reject",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        if(response.status == 200){
          this.defaultborrowing();
          this.clearview();
        }
      });
    },
    // 6 - Delete borrowing
    fn_delete() {
      this.ex_delete(this.currentview.api_id).then((response) => {
        this.$bvToast.toast(response.message, {
          title: this.updatemode == 3 ? "Approve" : "Return",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        if (response.status == 200) {
          this.preview = false;
          this.currentview = this.activeonly[0];
        }
        this.clearview()
   });
    },

    get_campusheader() {
      return this.currentview.api_role == 1 ? "NIM" : "Staff ID";
    },
    get_campusidentity() {
      return this.currentview.api_role == 1
        ? this.currentview.api_nim
        : this.currentview.api_staffid;
    },
    generatepassword() {
      let password =
        Math.random().toString(36).substring(2, 6) +
        Math.random().toString(36).substring(2, 8);
      this.passwordvalue = password;
      this.form.api_password = password;
    },
    getfacultyapi() {},
    fn_showcreatecampus() {
      this.show_createcampus = true;
    },
    abbreviate(name) {
      var matches = name.match(/\b(\w)/g);
      var acronym = matches.join("");
      return acronym;
    },

    // Copy Faculty to Form

    // Edit and Update Faculty name and description
    updatefaculty() {
      let data = {
        faculty: this.currentview.api_id,
        api_name: this.facultyform.api_name,
        api_description: this.facultyform.api_description,
      };
      this.ex_putfaculty(data).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Update Faculty",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
      });
      this.clearFacultyView();
    },
    clearview() {
      this.preview = 0;
      this.getborrowings();
    },
    clearFacultyView() {
      this.preview = 0;
      this.getfaculties();
      this.settab = 2;
    },
     customsearch() {
      let result = this.apiborrowings.filter((borrowing) => {
        return (borrowing.api_id == this.filterinput)
      });
      if (result.length > 0) {
        this.sample = result;
        this.pendingborrowing = result;
        this.$bvToast.toast('Borrowing found', {
          title: "Approve",
          variant: "primary",
          solid: true,
        });
      } else {
        this.searchempty = true;
      }
    },
    closepreview() {
      this.preview = false;
    },
    uploadImage(e) {
      const file = e.target.files[0];
      this.file = e.target.files[0];
      this.imagepreviewer = URL.createObjectURL(file);
      URL.revokeObjectURL(file); // free memory
    },
    clearPicture() {
      this.campuscreateform.api_thumbnail = [];
      this.imagepreviewer = "";
      this.file = ""
    },
    setThumbnail(){
      return this.file ? this.file : null
    },





    //BORROWINGS
      getborrowings() {
      this.ex_getborrowings().then((response) => {
        this.apiborrowings = response.data;
        this.settab = 1;
      });

    },
   getfaculty(university){
      return `${university.faculty+'@'+university.campus}`
    },
    thisbookingstatus(status) {
      switch (status) {
        case 0:
          return "Pending";
        case 1:
          return "Approved";
        case 2:
          return "Completed";
        default:
          return "N/A";
      }
    },
        thisapprovedby() {
      return this.currentview.api_request.approvedby ?
        this.currentview.api_request.approvedby :
        "N/A";
    },

        getusers() {
      this.ex_getusers().then((response) => {
        this.formButton = "Create";
        this.userscollections = response.data;
      });
      this.apiitems().then((response) => {
        this.itemscollections = response.data;
      });
    },
          create_userpreview(id){
      this.userscollections.forEach((user)=>{
        if(user.api_id == id){
          this.displayselecteduser = user;
        }
      });
    },
        create_itempreview(id){
      this.itemscollections.forEach((item)=>{
        if(item.api_id == id){
          this.displayselecteditem = item;
        }
      });
    },    sendMessage() {
      let messagedata = {
        recipient: this.currentview.api_borrower.api_id,
        subject: 'booking id : '+ this.currentview.api_id,
        message: this.messageform,
      };
      this.ex_postnewmessage(messagedata).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Message",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
      });
    },
    defaultborrowing(){
      this.createform = {}
    this. displayselecteduser = {}
    this.displayselecteditem = {}
    },
  },

  computed: {
    ...mapGetters(["get_user"]),
    searchtype() {
      return this.settab;
    },
    filterborrowing() {
        return (this.settab == 1 ? this.activeonly : this.allborrowing).filter((borrowing) => {
        return (
          borrowing.api_id.toString().includes(this.filterinput.toString()) || 
          borrowing.api_borrower.api_name.toLowerCase().includes(this.filterinput.toLowerCase()) || 
          borrowing.api_item.api_name.toLowerCase().includes(this.filterinput.toLowerCase()) || 
          borrowing.api_borrowdate.includes(this.filterinput.toLowerCase()));
      })
      
      // let all = this.activeonly.slice((this.currentPage - 1) * this.perPage, this.currentPage * this.perPage)
      // return all.filter((borrowing) => {
      //   return (borrowing.api_borrower.api_name.toLowerCase().includes(this.filterinput.toLowerCase()) || borrowing.api_item.api_name.toLowerCase().includes(this.filterinput.toLowerCase()) || borrowing.api_borrowdate.includes(this.filterinput.toLowerCase()));
      // })
    },
    checkcampus() {
      return this.campusform.api_campus;
    },
    whichmodal() {
      return this.setmodal;
    },
    previewer() {
      return this.preview;
    },  
    watchtab() {
      return this.settab
    },

  },
  watch: {
    searchtype() {
      this.previewreload = true;
      setTimeout(() => {
        this.previewreload = false;
        this.preview = false;
      }, 1000);
    },
    checkcampus() {
      let index = this.campus.findIndex(
        (campus) => campus.api_id == this.campusform.api_campus
      );
      this.campusfaculties = this.campus[index].api_faculties;
    },
    whichmodal() {
      this.show_createcampus = true;
    },
    watchtab(val) {
      this.preview = false;
      this.currentview = {}
      this.filterinput = ""
      this.pendingborrowing = {};
      if(val == 1){
       this.activeonly = this.apiborrowings.filter((borrowing) => {
          return (borrowing.api_request.status == 1)
      });
      }
      else{
        this.allborrowing = this.apiborrowings
      }
    },
  },
  created() {
        this.getborrowings();
  },
};
</script>

