<template>
  <div class="page-home">
    <b-container >
      <div class="">
        <template v-if="successful">
          <success-notify></success-notify>
          <hr />
          <h3>{{ notifyuser }}</h3>
        </template>
        <template v-else>
          <b-form class="form" @submit.prevent.once="onRegister">
            <b-form-group id="input-group-1">
              <b-form-input
                id="input-1"
                v-model="registerform.api_firstname"
                required
                placeholder="First name"
                :class="{ error: printerros.name }"
                @change="watchman('name')"
              ></b-form-input>
              <span class="errortext" v-if="printerros.name">
                {{ printerros.nameerror }}
              </span>
            </b-form-group>
            <b-form-group id="input-group-2">
              <b-form-input
                id="input-2"
                v-model="registerform.api_othernames"
                required
                placeholder="Middle & Last name"
                :class="{ error: printerros.othernames }"
                @change="watchman('othernames')"
              ></b-form-input>
              <span class="errortext" v-if="printerros.othernames">
                {{ printerros.othernameserror }}
              </span>
            </b-form-group>
            <b-form-group id="input-group-3">
              <b-form-input
                id="input-3"
                v-model="registerform.api_email"
                type="email"
                required
                :class="{ error: printerros.email }"
                placeholder="Enter email"
                @change="watchman('email')"
              ></b-form-input>
              <span class="errortext" v-if="printerros.email">
                {{ printerros.emailerror }}
              </span>
            </b-form-group>

            <b-form-group id="input-group-4">
              <b-form-input
                id="input-4"
                v-model="registerform.api_nim"
                required
                placeholder="Enter NIM"
                type="number"
                :class="{ error: printerros.nim }"
                @change="watchman('nim')"
              ></b-form-input>
              <span class="errortext" v-if="printerros.nim">
                {{ printerros.nimerror }}
              </span>
            </b-form-group>
            <b-form-group id="input-group-5">
              <b-form-input
                id="input-5"
                v-model="registerform.api_contact"
                required
                placeholder="Enter Contact"
                type="number"
                :class="{ error: printerros.contact }"
                @change="watchman('contact')"
              ></b-form-input>
              <span class="errortext" v-if="printerros.contact">
                {{ printerros.contacterror }}
              </span>

            </b-form-group>

            <b-form-group id="input-group-6">
              <b-form-input
                id="input-6"
                v-model="registerform.api_password"
                required
                placeholder="Enter password"
                type="password"
                :class="{ error: printerros.password }"
                @change="watchman('password')"
              ></b-form-input>
              <span class="errortext" v-if="printerros.password">
                {{ printerros.passworderror }}
              </span>
            </b-form-group>

            <b-form-group id="input-group-7">
              <b-form-input
                id="input-7"
                v-model="registerform.api_cpassword"
                required
                placeholder="Enter confirm password"
                type="password"
                :class="{ error: printerros.password }"
                @change="watchman('password')"
              ></b-form-input>
              <span class="errortext" v-if="printerros.password">
                {{ printerros.passworderror }}
              </span>
            </b-form-group>

            <b-form-group id="input-group-8">
              <b-form-select
                id="input-8"
                v-model="registerform.api_campus"
                value-field="api_id"
                text-field="api_name"
                :options="campuses"
                placeholder="Choose Campus"
                required
                :class="{ error: printerros.campus }"
                @change="watchman('campus')"
              ></b-form-select>
              <span class="errortext" v-if="printerros.campus">
                {{ printerros.campuserror }}
              </span>
            </b-form-group>
            <b-form-group id="input-group-9">
              <b-form-select
                id="input-9"
                v-model="registerform.api_faculty"
                :options="filterfaculty"
                :disabled="!faculty"
                value-field="api_id"
                text-field="api_name"
                placeholder="Choose Faculty"
                required
                :class="{ error: printerros.faculty }"
                @change="watchman('faculty')"
              ></b-form-select>
              <span class="errortext" v-if="printerros.faculty">
                {{ printerros.facultyerror }}
              </span>
            </b-form-group>
            <b-button
              type="submit"
              @click="onRegister"
              class="btn btn-block p-3 app_submitbutton"
              variant="default"
              >Register</b-button
            >
          </b-form>
        </template>
      </div>
    </b-container>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import SuccessNotify from "../Success";

export default {
  components: {
    SuccessNotify,
  },
  data() {
    return {
      successful: false,
      haserror: false,
      notifyuser: "",
      apicampusescollection: [],
      faculty: [],
      registerform: {
        api_firstname: "",
        api_othernames: "",
        api_email: "",
        api_nim: "",
        api_picture:
          "",
        api_password: "",
        api_contact: "",
        api_cpassword: "",
        api_campus: "",
        api_faculty: "",
      },
      printerros: {
        name: false,
        othernames: false,
        email: false,
        contact: false,
        faculty: false,
        campus: false,
        password: false,
      },
      errorlist: [],
      campuses: [],
    };
  },
  methods: {
    //LOGIN
    ...mapActions({
      ex_campusfaculties: "store_campuses/ex_campusfaculties",
      ex_register: "ex_register",
    }),
    reset() {
      for (const formvalue in this.registerform) {
        this.registerform[formvalue] = "";
      }
    },
    // REGiSTER FORM
    onRegister(evt) {
      evt.preventDefault();
      this.ex_register(this.registerform).then((response) => {
        this.notifyuser = response.message;
        this.$bvToast.toast(this.notifyuser, {
          title: "Sign-up ",
          variant: response.status == 200 ? "success" : "danger",
          solid: true,
        });

        if (response.status == 200) {
          this.successful = true;
          this.reset();
        } else {
          this.haserror = true;
          this.errorlist = response.data;
        }
      });
    },
    getfaculty() {
      this.campuses.forEach((campus) => {
        if (this.registerform.api_campus == campus.api_id) {
          this.faculty = campus.api_faculties;
        }
      });
    },
    geterrors() {
      for (const [key, value] of Object.entries(this.errorlist)) {
        switch (key) {
          case "firstname":
            this.printerros.name = true;
            this.printerros.nameerror = value[0];
            break;
          case "othernames":
            this.printerros.othernames = true;
            this.printerros.othernameserror = value[0];
            break;
          case "email":
            this.printerros.email = true;
            this.printerros.emailerror = value[0];
            break;          
          case "nim":
            this.printerros.nim = true;
            this.printerros.nimerror = value[0];
            break;
          case "campus":
            this.printerros.campus = true;
            this.printerros.campuserror = value[0];

            break;
          case "contact":
            this.printerros.contact = true;
            this.printerros.contacterror = value[0];

            break;
          case "password":
            this.printerros.password = true;
            this.printerros.passworderror = value[0];
            break;
          case "faculty":
            this.printerros.faculty = true;
            this.printerros.facultyerror = value[0];
            break;
        }
        // this.printerros.push(`${key}: ${value}`);
      }
    },
    watchman(field) {
      if (this.printerros[field]) {
        this.printerros[field] = false;
      }
    },
  },
  computed: {
    activecampus() {
      return this.registerform.api_campus;
    },
    filterfaculty() {
      return this.faculty;
    },
    errorwatcher() {
      return this.errorlist ? this.errorlist : false ;
    },
  },
  watch: {
    watchsize: function () {
      alert("changed");
    },
    activecampus() {
      this.getfaculty();
    },
    errorwatcher() {
      this.geterrors();
    },
    watchform() {
      this.clearcurrent();
    },
  },
  created() {
    this.ex_campusfaculties().then((response) => {
      this.campuses = response.data.filter((campuses) =>{
        return campuses.api_name.toLowerCase() != 'admin'
      });
    });
  },
};
</script>

