<template>
  <b-container fluid>
    <div class="app_main_wrapper">
      <b-container>
        <div class="currenSection">
          <h5
            class="css-indicator-nav"
            style="padding-left: 20px; padding-right: 40px"
            @click="navback"
          >
            <b-icon icon="chevron-left"></b-icon>Back
          </h5>
          <h5 class="css-indicator-active">Password Settings</h5>
        </div>
        <div class="form-parent app_rounded app_topmargin">
          <template v-if="complete">
            <success-notification></success-notification>
            <hr />
          </template>
          <template v-else>
            <h3 class="app_pageheader app_20padding app_bottommargin">
              <span class="passwordIcon"
                ><b-icon icon="three-dots" aria-hidden="true"></b-icon
              ></span>
              User Password Setting
            </h3>
            <div class="">
              <p>You must provide your current password...</p>
            </div>
            <b-form @submit="fn_change">
              <b-form-group id="input-group-1" label-for="input-1">
                <b-form-input
                  id="input-1"
                  v-model="form.current"
                  type="password"
                  required
                  placeholder="Enter Current Password"
                  :class="{ error: printerros.new_password }"
                  @change="clearerror('current')"
                ></b-form-input>
              </b-form-group>
              <p class="hint" v-if="promt">{{ printerros.currenterror }}</p>

              <p class="hint">Use a strong password</p>

              <b-form-group id="input-group-2" label-for="input-2">
                <b-form-input
                  id="input-2"
                  v-model="form.new_password"
                  type="password"
                  required
                  placeholder="Enter new Password"
                  :class="{ error: printerros.new_password }"
                  @change="clearerror('new_password')"
                ></b-form-input>
              </b-form-group>
              <b-form-group id="input-group-3" label-for="input-3">
                <b-form-input
                  id="input-3"
                  v-model="form.confirm_password"
                  type="password"
                  required
                  placeholder="Confirm new Password"
                  :class="{ error: printerros.confirm_password }"
                  @change="clearerror('confirm_password')"
                ></b-form-input>
              </b-form-group>

              <b-form-group
                id="input-group-4"
                label-for="input-4"
                v-if="printerros"
              >
                <b-list-group v-if="haserror">
                  <b-list-group-item
                    class="app_roundedlist app_notifyerror d-flex justify-content-start align-items-center"
                    v-for="(error, index) in errorlist"
                    :key="index"
                    ><b-badge variant="danger" pill>{{ index }}</b-badge>
                    <span class="app_listchildright">{{
                      error[0]
                    }}</span></b-list-group-item
                  >
                </b-list-group>
              </b-form-group>             
              <b-form-group
                id="input-group-4"
                label-for="input-4"
                v-if="haserror"
              >
                          <p class="css_errormessage">{{ message }}</p>

              </b-form-group>
              <b-button
                class="app_button app_topmargin"
                type="submit"
                variant="primary"
                >Update Password</b-button
              >
            </b-form>
          </template>
        </div>
      </b-container>
    </div>
  </b-container>
</template>

<script>
import { mapActions } from "vuex";
import SuccessNotification from "../Success";

export default {
  name: "app-message",
  components: {
    SuccessNotification,
  },
  data() {
    return {
      complete: false,
      status: 0,
      message: "",
      promt: false,
      haserror: false,
      printerros: {
        current: false,
        new_password: false,
        confirm_password: false,
      },
      errorlist: [],
      form: {
        current: "",
        new_password: "",
        confirm_password: "",
      },
      show: true,
    };
  },
  methods: {
    ...mapActions(["exdelete", "ex_changepassword"]),
    navback() {
      this.$router.push({
        name: "Settings",
      });
    },
    close() {
      this.exdelete();
    },
    fn_change(evt) {
      console.log()
      evt.preventDefault();
      if (this.checkchar()) {
        this.ex_changepassword(this.form).then((response) => {
          this.$bvToast.toast(response.message, {
            title: "Password Settings",
            variant: response.status == 200 ? "success" : "danger",
            solid: true,
          });
          this.message = response.message;
          if (response.status == 200) {
            this.complete = true;
            this.fn_reset();
          }
          if (response.status == 400) {
            this.haserror = true;
            this.errorlist = response.data ? response.data : [];
          }
        });
      }
    },
    fn_reset() {
        this.form.current = "";
        this.form.new_password = "";
        this.form.confirm_password = "";
      this.errorlist = [];
      this.haserror = false;

    },
    clearerror(field) {
      this.printerros[field] = false;
      delete this.errorlist[field];
    },
    geterrors() {
      for (const [key, value] of Object.entries(this.errorlist)) {
        switch (key) {
          case "current":
            this.printerros.current = true;
            this.printerros.currenterror = value;
            break;
          case "new_password":
            this.printerros.new_password = true;
            this.printerros.new_passworderror = value[0];
            break;
          case "confirm_password":
            this.printerros.confirm_password = true;
            this.printerros.confirm_passworderror = value[0];
            break;
        }
      }
    },
    closepromt(){
      setTimeout(() => {
        this.promt = false;
      }, 3000);
    },
    checkchar() {
      if (this.form.current.length < 6) {
        this.promt = true;
        this.printerros.currenterror = "Password is too short. Try again";        
        return 0;
      }
      return 1;
    },
  },
  computed: {
    errorwatcher() {
      return this.errorlist;
    },
    wactchpromt(){
      return this.promt;
    }
  },
  watch: {
    errorwatcher() {
      this.geterrors();
    },
    wactchpromt(){
      this.closepromt();
    }
  },
  created() {},
};
</script>

