                      class="d-flex justify-content-between align-items-center"
                    >
                      Campus
                      <p>{{ currentview.api_name }}</p>
                    </b-list-group-item>
                    <b-list-group-item
                      class="d-flex justify-content-between align-items-center"
                    >
                      Email
                      <p>{{ currentview.api_email }}</p> </b-list-group-item
                    ><b-list-group-item
                      class="d-flex justify-content-between align-items-center"
                    >
                      Contact
                      <p>{{ currentview.api_contact }}</p> </b-list-group-item
                    ><b-list-group-item
                      class="d-flex justify-content-between align-items-center"
                    >
                      <!-- {{ currentuser.api_role == 1 ? "NIM" : "Staff ID" }}
              <p>
                {{ form.api_nim }}
              </p> -->
                    </b-list-group-item>
                  </b-list-group>
                </template>
              </b-card>
            </b-form>
          </template>
          <template v-if="updatemode == 5 || updatemode == 6">
            <b-form-group label="Name">
              <b-form-input
                v-model="facultyform.api_name"
                type="text"
                required
                placeholder="Enter Faculty Name"
              ></b-form-input>
            </b-form-group>
            <b-form-group label="Address">
              <b-form-textarea
                id="textarea"
                v-model="facultyform.api_description"
                placeholder="Enter Faculty Description..."
                rows="3"
                max-rows="6"
              ></b-form-textarea>
            </b-form-group>
          </template>
          <template v-if="updatemode == 7">
            <div class="css-deleteid">
              <small>FACULTY ID</small>
              <h5>{{ currentview.api_id }}</h5>
            </div>
            <b-list-group>
              <h3>Faculty Information</h3>

              <b-list-group-item
                class="d-flex justify-content-between align-items-center"
              >
                Name
                <p>{{ currentview.api_name }}</p>
              </b-list-group-item>
              <b-list-group-item
                class="d-flex justify-content-between align-items-center"
              >
                Description
                <p>{{ currentview.api_description }}</p>
              </b-list-group-item>
            </b-list-group>
          </template>
        </b-card>
      </b-form>
      <template #modal-footer="{ ok, cancel }">
        <div class="w-100">
          <b-button
            class="float-left"
            variant="outline-danger"
            @click="cancel()"
          >
            Cancel
          </b-button>
          <b-button
            class="float-right"
            :variant="formButton != 'Delete' ? 'success' : 'danger'"
            @click="ok()"
          >
            {{ formButton }}
          </b-button>
        </div>
      </template>
    </b-modal>
    <b-modal
      ref="modal"
      centered
      id="modal-confirmation"
      header-bg-variant="danger"
      header-text-variant="light"
      title="Confirm Action"
      @ok="confirm_modelAction"
      @cancel="onConfirmCancel"
    >
      <b-card ref="form" @submit.stop.prevent="modalConfirmAction">
        <div class="text-center">
          <b-icon
            icon="exclamation-circle"
            style="width: 80px; height: 80px"
          ></b-icon>
          <h5>Are you sure about this ?</h5>
        </div>
      </b-card>
      <template #modal-footer="{ ok, cancel }">
        <div class="w-100">
          <b-button
            class="float-left"
            variant="outline-danger"
            @click="cancel()"
          >
            Cancel
          </b-button>
          <b-button class="float-right" variant="danger" @click="ok()">
            Continue
          </b-button>
        </div>
      </template>
    </b-modal>
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import EmptyContent from "./EmptyViews/EmptyContent.vue";
import EmptyPreview from "./EmptyViews/EmptyPreview";

export default {
  name: "app_campusescollection",
  components: {
    EmptyContent,
    EmptyPreview,
  },
  data() {
    return {
      apicampusescollection: [],
      apifacultycollection: [],
            imagepreviewer: "",
            file: "",
      setmodal: 0,
      updatemode: 0,
      confirmation: false,
      rows: 100,
      perPage: 1,
      viewingitem: "",
      currentPage: 1,
      settab: 1,
      preview: false,
      previewindex: "-",
      tabIndex: 0,
      facultyedit: "false",
      addfacultyform: {
        api_id: "",
        api_thumbnail: [],
      },
      facultyform: {
        api_name: "",
        api_description: "",
      },
      currentview: {},

      campusform: {
        api_name: "",
        api_thumbnail: [],
        api_description: "",
        api_address: "",
        api_email: "",
      },

      campuscreateform: {
        api_name: "",
        api_thumbnail: [],
        api_description: "",
        api_address:
          "",
        api_contact: "",
        api_email: "",
      },

      passwordvalue: "",
      selected: "A",
      show_updatemodal: false,
      show_createcampus: false,
      show_deletecampus: false,
      previewreload: false,
      search: "",
      filterinput: "",
      pendingcampus: [],
      variant: "dark",
      role: [
        { value: 1, text: "Student" },
        { value: 2, text: "Staff" },
        { value: 3, text: "Admin" },
      ],
      campus: [],
      faculty: [],
      campusfaculties: [],
      formButton: "",
    };
  },
  methods: {
    ...mapActions({
      ex_campusfaculties: "store_campuses/ex_campusfaculties",
      apicampuses: "store_campuses/apicampuses",
      ex_campuses: "store_campuses/ex_campuses",
      ex_campusupdate: "store_campuses/ex_campusupdate",
      ex_querycampusfaculty: "store_campuses/ex_querycampusfaculty",
      ex_addcampusfaculty: "store_campuses/ex_addcampusfaculty",
      ex_faculties: "store_campuses/ex_faculties",
      ex_putfaculty: "store_campuses/ex_putfaculty",
      ex_getfaculties: "store_campuses/ex_getfaculties",
      ex_unmapfaculty: "store_campuses/ex_unmapfaculty",
      ex_deletecampus: "store_campuses/ex_deletecampus",
      ex_deletefaculty: "store_campuses/ex_deletefaculty",
    }),
    tabAction(tab) {
      tab == 1 ? this.getcampuses() : this.getfaculties();
    },
    confirm_modelAction(bvModalEvt) {
      bvModalEvt.preventDefault();
      this.modalConfirmAction();
    },
    modalConfirmAction() {
      this.confirmation
        ? this.$nextTick(() => {
            this.$bvModal.hide("modal-confirmation");
            this.$bvModal.show("modal-forms");
            this.confirmation = false;
          })
        : this.$bvModal.show("modal-forms");
    },
    onConfirmCancel() {
      this.confirmation = false;
    },
    // Get campuses...
    getcampuses() {
      this.ex_campusfaculties().then((response) => {
                this.apicampusescollection = response.data.filter(function(item) {
    return item.api_id !== 20201205125211
})
        this.settab = 1;
      });
    },
    // Get Faculties
    getfaculties() {
      this.ex_getfaculties().then((response) => {
        this.apifacultycollection = response.data.filter((faculty)=>{
          return faculty.api_name != 'admin';
        })
      });
      this.settab = 2;
    },
    // Preview Campus
    setpreview(index) {
      this.previewreload = true;
      setTimeout(() => {
        this.preview = true;
        this.previewindex = index;
        this.currentview =
          this.settab == 1
            ? JSON.parse(JSON.stringify(this.filtercampuses[index]))
            : JSON.parse(JSON.stringify(this.filterfaculties[index]));

        this.previewreload = false;
      }, 500);
    },
    // Forms
    formaction(type) {
      this.updatemode = type;
      switch (type) {
        case 1:
          // Create campus
          this.formButton = "Create";
          break;
        case 2:
          // Update campus
          this.campusform = JSON.parse(JSON.stringify(this.currentview));
          this.formButton = "Update";
          break;
        case 3:
          // Map faculty - Getting available faculties first
          this.formButton = "Add";
          this.fn_beforecampusfaculty();
          break;
        case 4:
          // Delete campus
          this.formButton = "Delete";
          this.confirmation = true;
          break;
        case 5:
          // Create faculty
          this.formButton = "Create";
          this.facultyform.api_name = "";
          this.facultyform.api_description = "";
          break;
        case 6:
          // Update faculty
          this.formButton = "Update";
          this.facultyform = JSON.parse(JSON.stringify(this.currentview));
          break;
        case 7:
          // Delete faculty
          this.formButton = "Delete";
          this.confirmation = true;
          break;
      }
      this.confirmation
        ? this.$bvModal.show("modal-confirmation")
        : this.$bvModal.show("modal-forms");
    },
    geturl(url) {
      let itemfetech = this.$router.resolve({
        name: "Itemdetails",
        params: {
          item: url,
        },
      });
      window.open(itemfetech.href, "_blank");
    },
    countcampus() {
      return this.apicampusescollection.length;
    },
    countfaculty() {
      return this.apifacultycollection.length;
    },
    fn_cancelupdate() {
      this.show_updatemodal = false;
      this.form = {};
    },
    form_navigator() {
      switch (this.updatemode) {
        case 1:
          this.fn_addcampus();
          break;
        case 2:
          this.fn_updatecampus();
          break;
        case 3:
          this.fn_campusfaculty();
          break;
        case 4:
          this.fn_deletecampus();
          break;
        case 5:
          this.fn_addfaculty();
          break;
        case 6:
          this.updatefaculty();
          break;
        case 7:
          this.fn_deletefaculty();
          break;
      }
    },

    // 1 - Add a campus
    fn_addcampus() {
      let data = new FormData();
      data.append("api_id", this.campuscreateform.api_id);
      data.append("api_name", this.campuscreateform.api_name);
      data.append("api_description", this.campuscreateform.api_description);
      data.append("api_thumbnail", this.setThumbnail());
      data.append("api_address", this.campuscreateform.api_address);
      data.append("api_contact", this.campuscreateform.api_contact);
      data.append("api_email", this.campuscreateform.api_email);

      this.ex_campuses(data).then((response) => {
        if (response.status == 200) {
          this.show_createcampus = false;
          this.defaultcampus();
      this.clearview();
        }
        this.$bvToast.toast(response.message, {
          title: "Add campus",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
      });
    },
    // 2 - update
    fn_updatecampus() {
      let data = new FormData();
      data.append("_method", "PUT");
      data.append("api_id", this.currentview.api_id);
      data.append("api_name", this.campusform.api_name);
      data.append("api_description", this.campusform.api_description);
      data.append("api_thumbnail", this.setThumbnail());
      data.append("api_address", this.campusform.api_address);
      data.append("api_contact", this.campusform.api_contact);
      data.append("api_email", this.campusform.api_email);
      this.ex_campusupdate(data).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Update Campus",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
                        if (response.status == 200) {
      this.clearview();
        }
      });
    },

    //3 - Get available faculties
    fn_beforecampusfaculty() {
      this.ex_querycampusfaculty(this.currentview.api_id).then((response) => {
        // this.faculty = this.currentview.api_faculties.filter(element => response.data.includes(element));
        this.faculty = response.data
      });
    },

    //3 - Add/Map faculty to a campus
    fn_campusfaculty() {
      let data = {
        api_faculty: this.addfacultyform.api_id,
        api_campus: this.currentview.api_id,
      };
      this.ex_addcampusfaculty(data).then((response) => {
      if(response.status == 200){
      this.$bvToast.toast(response.message, {
          title: this.formButton,
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
      this.defaultfaculty();
      this.clearview();
      }
      });
    },

    //4 - Unmap a faculty
    fn_unmapfaculty() {
      let data = {
        api_id: this.facultyedit,
        api_campus: this.currentview.api_id,
      };
      this.ex_unmapfaculty(data).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Delete Faculty",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
      });
      this.clearview();
    },

    //5 - Unmap a faculty
    fn_deletecampus() {
      this.ex_deletecampus(this.currentview.api_id).then((response) => {
        this.$bvToast.toast(response.message, {
          title: response.status == 200 ? "Successful" : "Failed",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
                if (response.status == 200) {
      this.clearview();
        }
      });
    },
    //6 - Add a faculty
    fn_addfaculty() {
      this.ex_faculties(this.facultyform).then((response) => {
        if (response.status == 200) {
          this.show_createcampus = true;
        }
        this.$bvToast.toast(response.message, {
          title: "Add faculty",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
      });
      this.clearFacultyView();
    },
    // 7 - Delete a fculty
    fn_deletefaculty() {
      this.ex_deletefaculty(this.currentview.api_id).then((response) => {
        if (response.status == 200) {
          this.show_createcampus = true;
          this.clearview();
                this.clearFacultyView();
        }
        this.$bvToast.toast(response.message, {
          title: "Delete Faculty",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
      });
    },

    get_campusheader() {
      return this.currentview.api_role == 1 ? "NIM" : "Staff ID";
    },
    get_campusidentity() {
      return this.currentview.api_role == 1
        ? this.currentview.api_nim
        : this.currentview.api_staffid;
    },
    generatepassword() {
      let password =
        Math.random().toString(36).substring(2, 6) +
        Math.random().toString(36).substring(2, 8);
      this.passwordvalue = password;
      this.form.api_password = password;
    },
    getfacultyapi() {},
    fn_showcreatecampus() {
      this.show_createcampus = true;
    },
    abbreviate(name) {
      var matches = name.match(/\b(\w)/g);
      var acronym = matches.join("");
      return acronym;
    },

    // Copy Faculty to Form

    // Edit and Update Faculty name and description
    updatefaculty() {
      let data = {
        faculty: this.currentview.api_id,
        api_name: this.facultyform.api_name,
        api_description: this.facultyform.api_description,
      };
      this.ex_putfaculty(data).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Update Faculty",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
      });
      this.clearFacultyView();
    },
    clearview() {
      this.preview = 0;
      this.currentview = {};
      this.getcampuses();
    },
    clearFacultyView() {
      this.preview = 0;
      this.currentview = {};
      this.getfaculties();
      this.settab = 2;
    },
    customsearch() {},
    closepreview() {
      this.preview = false;
      this.viewingitem = "-";
      this.previewindex = "-"
    },
        uploadImage(e) {
      const file = e.target.files[0];
      this.file = e.target.files[0];
      this.imagepreviewer = URL.createObjectURL(file);
      URL.revokeObjectURL(file); // free memory
    },
        clearPicture() {
      this.campuscreateform.api_thumbnail = [];
      this.imagepreviewer = "";
      this.file = ""
    },
    setThumbnail(){
      return this.file ? this.file : null
    },
    defaultcampus(){
      this.imagepreviewer = "";
      this.campuscreateform.api_name = "";
      this.campuscreateform.api_thumbnail = [];
      this.campuscreateform.api_description = "";
      this.campuscreateform.api_address = "";
      this.campuscreateform.api_contact = "";
      this.campuscreateform.api_email = "";
    },
    defaultfaculty(){
      this.facultyform.api_name = "";
      this.facultyform.api_description = "";
    }
  },
  computed: {
    ...mapGetters(["get_user"]),
    searchtype() {
      return this.settab;
    },
    filtercampuses() {
      return this.apicampusescollection.filter((campus) => {
        return campus.api_name
          .toLowerCase()
          .includes(this.filterinput.toLowerCase());
      });
    },
    filterfaculties() {
      return this.apifacultycollection
        ? this.apifacultycollection.filter((faculty) => {
            return faculty.api_name
              .toLowerCase()
              .includes(this.filterinput.toLowerCase());
          })
        : null;
    },
    checkcampus() {
      return this.campusform.api_campus;
    },
    whichmodal() {
      return this.setmodal;
    },
    previewer() {
      return this.preview;
    },
  },
  watch: {
    searchtype() {
      this.previewreload = true;
      setTimeout(() => {
        this.previewreload = false;
        this.preview = false;
      }, 1000);
    },
    checkcampus() {
      let index = this.campus.findIndex(
        (campus) => campus.api_id == this.campusform.api_campus
      );
      this.campusfaculties = this.campus[index].api_faculties;
    },
    whichmodal() {
      this.show_createcampus = true;
    },
  },
  created() {
    this.getcampuses();
  },
};
</script>

