<template>
  <b-container fluid>
    <div class="app_main_wrapper">
      <b-container>
        <div class="currenSection">
          <h5
            class="css-indicator-nav"
            style="padding-left: 20px; padding-right: 40px"
            @click="navback"
          >
            <b-icon icon="chevron-left"></b-icon>Back
          </h5>
          <h5 class="css-indicator-active">Profile Settings</h5>
        </div>
        <div class="form-parent app_rounded app_topmargin">
          <template v-if="complete">
            <success-notification></success-notification>
            <hr />
            <h3>{{ notifyuser }}</h3>
          </template>
          <template v-else>
            <h3 class="app_pageheader app_20padding app_bottommargin">
              <b-icon icon="image-fill" aria-hidden="true"></b-icon> Profile
              Picture Setting
            </h3>
            <div class="this-center">
              <b-card
                img-top
                tag="article"
                bg-variant="white"
                align="center"
                header-border-variant="danger"
                style="max-width: 30rem"
                class="mb-2"
              >
                <b-card-text>
           <div id="preview" class="mb-6" v-if="imagepreviewer">
                <b-img fluid height="500" :src="imagepreviewer" :alt="user.api_name" rounded="circle"/>
                <b-icon @click="clearPicture" class="app_closefix" font-scale="2" icon="x-circle-fill" variant="danger"></b-icon>
                <hr />
              </div>
                                <b-img
                                v-else
                    :src="user.api_picture"
                    fluid
                    height="200"
                    :alt="user.api_firstname"
                  ></b-img>
                </b-card-text>
              </b-card>
            </div>
            <b-form @submit="onSubmit" enctype="multipart/form-data">
            <b-form-group id="input-group-0">
                <b-input-group>
                  <b-input-group-prepend is-text>
                    <b-icon icon="image-fill"></b-icon>
                  </b-input-group-prepend>
                  <b-form-file id="form-image" placeholder="Choose a file or drop it here..." drop-placeholder="Drop file here..." v-model="upload_picture" @change="uploadImage" accept="image/*"></b-form-file>
                </b-input-group>
          </b-form-group>

              <b-form-group
                id="input-group-5"
                label-for="input-5"
                v-if="printerros"
              >
                <b-list-group v-if="haserror">
                  <b-list-group-item
                    class="app_roundedlist app_notifyerror d-flex justify-content-start align-items-center"
                    v-for="(error, index) in errorlist"
                    :key="index"
                    ><b-badge variant="danger" pill>{{ index }}</b-badge>
                    <span class="app_listchildright">{{
                      error[0]
                    }}</span></b-list-group-item
                  >
                </b-list-group>
              </b-form-group>
              <b-button
                class="app_button app_topmargin"
                type="submit"
                :disabled="!formChanged"
                variant="primary"
                >Upload Picture</b-button
              >
            </b-form>
          </template>
        </div>
      </b-container>
    </div>
  </b-container>
</template>

<script>
import { mapActions } from "vuex";
import SuccessNotification from "../Success";
export default {
  name: "app-message",
  components: {
    SuccessNotification,
  },
  data() {
    return {
      complete: false,
      status: "0",
      haserror: false,
      notifyuser: "",
      printerros: {
        firstname: false,
        email: false,
        contact: false,
        othernames: false,
      },
      errorlist: [],
      user: {},
      setnewpicture: [],
      file: "",
      imagepreviewer: "",
      upload_picture: []
    };
  },
  methods: {
    ...mapActions({
      exdelete: "exdelete",
      ex_profile: "ex_profile",
      ex_imaging: "ex_imaging",
      ex_updatecurrentpicture: "ex_updatecurrentpicture"
    }),
    navback() {
      this.$router.push({
        name: "Settings",
      });
    },
        uploadImage(e) {
      const file = e.target.files[0];
      this.file = e.target.files[0];
      this.imagepreviewer = URL.createObjectURL(file);
      URL.revokeObjectURL(file); // free memory
    },
    onChange(e) {
      this.file = e.target.files[0];
    },
    clearPicture() {
      this.file = [];
      this.imagepreviewer = "";
    },
    close() {
      this.exdelete();
    },
    onSubmit(evt) {
      evt.preventDefault();
      let data = new FormData();
      data.append("api_setprofile", this.file);

      this.ex_imaging(data).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Profile Settings",
          variant: response.status == 200 ? "success" : "danger",
          solid: true,
        });
        if (response.status == 200) {
          this.complete = true;
          this.ex_updatecurrentpicture(response.data);
          this.notifyuser = response.message;
          this.reset();
        } else {
          this.errorlist = response.data;
          this.haserror = true;
        }
      });
    },
    reset() {
      this.errorlist = [];
      this.haserror = false;
    },
    getusers() {
      this.ex_profile().then((response) => {
        this.user = response.data
      });
    },
    clearerror(field) {
      this.printerros[field] = false;
      delete this.errorlist[field];
    },
    geterrors() {
      for (const [key, value] of Object.entries(this.errorlist)) {
        switch (key) {
          case "firstname":
            this.printerros.firstname = true;
            this.printerros.firstnameerror = value[0];
            break;
          case "othernames":
            this.printerros.othernames = true;
            this.printerros.othernameserror = value[0];
            break;
          case "email":
            this.printerros.email = true;
            this.printerros.emailerror = value[0];
            break;
          case "contact":
            this.printerros.contact = true;
            this.printerros.contacterror = value[0];
            break;
        }
      }
    },
  },
  computed: {
    formChanged() {
      return this.setnewpicture;
    },
  },
  created() {
    this.getusers();
  },
};
</script>

