<script>
import { mapGetters } from "vuex";
export default {
  name: "app-message",
  data() {
    return {
      notify: false,
      status: "0",
      busy: false,
    };
  },
  methods: {
    getSettings(setting) {
            if (setting == 1) {
        this.$router.push({
          name: "SettingsPicture",
        });
      }
      if (setting == 2) {
        this.$router.push({
          name: "SettingsProfile",
        });
      }
      if (setting == 3) {
        this.$router.push({
          name: "SettingsPassword",
        });
      }
      if (setting == 4) {
        this.$router.push({
          name: "SettingsAccount",
        });
      }
    },
    selfnotify() {
      setTimeout(() => {
        this.notify = true;
      }, 3000);
      this.notify = false;
    },
  },
  computed: {
    ...mapGetters(["get_session", "get_usersession"]),
  },
  created() {
    this.selfnotify();
  },
};
</script>

