      return localStorage.getItem('role');
    },
  },
  methods: {
    actionRoute(routename) {
      this.$router.push({
        name: routename,
      });
    },
    useris(is) {
      switch (parseInt(is)) {
        case 1:
          return "Student";
        case 2:
          return "Staff";
        case 3:
          return "Admin";
      }
    },
  },
