<template>
  <b-container>
        <div class="accessroute">
          <button
            class="btn css-access"
            :class="{ cssactive: access == 1 }"
            @click="setaccess"
          >
            Student
          </button>
          <button
            class="btn css-access"
            :class="{ cssactive: access == 2 }"
            @click="setaccess"
          >
            Staff
          </button>
        </div>
        <div class="">
          <h5 class="css-indicator">
            {{ access == 1 ? "Student" : "Staff" }} Login Session
          </h5>
          <b-form @submit.prevent.once="onLogin">
            <b-form-group id="input-group-1" label-for="input-1">
              <b-form-input
                id="input-1"
                v-model="loginform.email"
                type="email"
                required
                :class="{ error: loginerror }"
                placeholder="Enter Login E-mail"
                @change="formChange"
              ></b-form-input>
            </b-form-group>

            <b-form-group
              id="input-group-2"
              label-for="input-2"
              v-if="access == 2"
            >
              <b-form-input
                id="input-2"
                type="text"
                v-model="loginform.sid"
                required
                :class="{ error: loginerror }"
                placeholder="Enter Staff ID"
                                @change="formChange"

              ></b-form-input>
            </b-form-group>

            <b-form-group id="input-group-3" label-for="input-3">
              <b-form-input
                id="input-3"
                type="password"
                v-model="loginform.password"
                required
                placeholder="Enter Password"
                :class="{ error: loginerror }"
                                @change="formChange"
              ></b-form-input>
            </b-form-group>
            <b-form-group id="input-group-4" label-for="input-4" v-if="loginerror">
              <small class="errortext" >
                {{ loginerror }}
              </small>
            </b-form-group>

            <b-button
            type="submit"
            @click="onLogin"
              class="btn btn-block p-3 app_submitbutton"
              :class="{ css_disabled: !middlewareinput }"
              :disabled="!middlewareinput"
              >Login</b-button
            >
          </b-form>
          <router-link to="/forgotpassword">
          <p class="app_link mt-5">Forgot password? Click here</p>
          </router-link>
        </div>
  </b-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  components: {},
  data() {
    return {
      loginerror: "",
      middlewareinput: true,
      loginform: {
        email: "",
        password: "",
        sid: "",
      },
      expand: false,
      access: 1,
      errorlist: [],
    };
  },
  methods: {
    //LOGIN
    ...mapActions(["ex_login"]),
    setaccess(evt) {
      evt.target.innerText.toLowerCase() == "student"
        ? (this.access = 1)
        : (this.access = 2);
    },
    onLogin(evt) {
      evt.preventDefault();
      this.ex_login(this.loginform).then((response) => {
        this.$bvToast.toast(response.status == 200 ? ('Welcome '+response.data.api_firstname) : response.message, {
          title: "Login",
          variant: response.status == 200 ? "success" : "danger",
          solid: true,
        });

        if (response.status == 200) {
          this.$router.push({ name: "Dashboard" });
        }
        else{
          this.loginerror = response.message;
        }
      });

    },
    reset() {
      for (const formvalue in this.loginform) {
        this.loginform[formvalue] = "";
      }

      for (const formvalue in this.registerform) {
        this.registerform[formvalue] = "";
      }
      this.access = 1;

      this.access = 1;
    },
    //LOGIN-END
      formChange(){
        if(this.loginerror){
          this.loginerror = ""
        }
      }

  },
computed: {
...mapGetters(['get_usersession']),
},
watch:{
}
};
</script>

