          <h1 class="app-header mb-5">BINUS UNIVERSITY</h1>
          <login-app v-if="showlogin"></login-app>
          <register-app v-else></register-app>
            <p class="app_hint mt-5 " >{{ showlogin ? 'Are you a new user?' : 'Already have an account ?'}}</p>
            <div class="text-center app-director ">
              <p class="mt-0">
                <b-icon
                  icon="chevron-right"
                  scale="1"
                  rotate="90"
                  shift-h="4"
                  variant="secondary"
                ></b-icon>
              </p>
              <p class="app_shader app_haslink"  @click="showlogin = !showlogin">{{ checkview() }}</p>
            </div>
        </b-container>
      </div>
                <b-container fluid class="otherlinks ">
        <b-container>
          <footer-index></footer-index>
        </b-container>
