<script>
import LoginApp from "@/components/Views/ActivityViews/Access/LoginComponent";
import RegisterApp from "@/components/Views/ActivityViews/Access/RegisterComponent";
import FooterIndex from "@/components/MainIndexes/FooterIndex";

export default {
  components: {
    LoginApp,
    FooterIndex,
    RegisterApp
  },
  data() {
    return {
      showlogin: true,
    };
  },
  methods: {
    watchmq() {
      //  var width = (window.innerWidth > 0) ? window.innerWidth : screen.width;
      var x = window.matchMedia("(max-width: 600px)");
      x.matches ? (this.expand = true) : (this.expand = false);
    },
    watchinput() {
      if (
        (this.loginform.access == 1 && this.loginform.email.length < 1) ||
        this.loginform.password.length < 1
      ) {
        return false;
      } else if (
        (this.loginform.access == 2 && this.loginform.email.length < 1) ||
        this.loginform.password.length < 1 ||
        this.loginform.sid.length < 1
      ) {
        return false;
      }
      return true;
    },
    checkview(){
      return this.showlogin ? "Register here" : "Login now";
    }
  },
  computed: {
    accessfor() {
      return this.access;
    },
    watchsize() {
      return this.watchmq();
    },

    middlewareinput() {
      return this.watchinput();
    },
  },
  watch: {
    watchsize: function () {
      alert("changed");
    },

    accessfor() {
      this.loginerror = false;
      this.errorlist = false;
    },
  },
  created() {},
};
</script>

