<template>
  <b-container fluid>
    <div class="app_main_wrapper">
      <b-container>
        <div class="currenSection">
          <h5
            class="css-indicator-nav"
            style="padding-left: 20px; padding-right: 40px"
            @click="navback"
          >
            <b-icon icon="chevron-left"></b-icon>Back
          </h5>
          <h5 class="css-indicator-active">Profile Settings</h5>
        </div>
        <div class="form-parent app_rounded app_topmargin">
          <template v-if="complete">
            <success-notification></success-notification>
            <hr />
            <h3>{{ notifyuser }}</h3>
          </template>
          <template v-else>
            <h3 class="app_pageheader app_20padding app_bottommargin">
              <b-icon icon="person-fill" aria-hidden="true"></b-icon> User
              Info Setting
            </h3>
            <b-form @submit.prevent.once="onSubmit">
              <b-form-group id="input-group-1" label-for="input-1">
                <b-form-input
                  id="input-1"
                  v-model="form.api_firstname"
                  type="text"
                  required
                  placeholder="First name"
                  @change="clearerror('firstname')"
                  :class="{ error: printerros.firstname }"
                ></b-form-input>
              </b-form-group>

              <b-form-group id="input-group-2" label-for="input-2">
                <b-form-input
                  id="input-2"
                  v-model="form.api_othernames"
                  placeholder="Othernames"
                  @change="clearerror('othernames')"
                  :class="{ error: printerros.othernames }"
                ></b-form-input>
              </b-form-group>
              <b-form-group id="input-group-3" label-for="input-3">
                <b-form-input
                  id="input-3"
                  v-model="form.api_email"
                  required
                  placeholder="Email"
                  @change="clearerror('email')"
                  :class="{ error: printerros.email }"
                ></b-form-input>
              </b-form-group>
              <b-form-group id="input-group-4" label-for="input-4">
                <b-form-input
                  id="input-3"
                  v-model="form.api_contact"
                  required
                  placeholder="Contact"
                  :class="{ error: printerros.contact }"
                  @change="clearerror('contact')"
                ></b-form-input>
              </b-form-group>
              <b-form-group
                id="input-group-5"
                label-for="input-5"
                v-if="printerros"
              >
                <b-list-group v-if="haserror">
                  <b-list-group-item
                    class="app_roundedlist app_notifyerror d-flex justify-content-start align-items-center"
                    v-for="(error, index) in errorlist"
                    :key="index"
                    ><b-badge variant="danger" pill>{{ index }}</b-badge>
                    <span class="app_listchildright">{{
                      error[0]
                    }}</span></b-list-group-item
                  >
                </b-list-group>
              </b-form-group>
              <b-button
                class="app_button app_topmargin"
                type="submit"
                @click="onSubmit"
                variant="primary"
                >Update Profile</b-button
              >
            </b-form>
          </template>
        </div>
      </b-container>
    </div>
  </b-container>
</template>

<script>
import { mapActions } from "vuex";
import SuccessNotification from "../Success";
export default {
  name: "app-message",
  components: {
    SuccessNotification,
  },
  data() {
    return {
      complete: false,
      status: "0",
      haserror: false,
      userhas: 0,
      notifyuser: "",
      password: "",
      backup: {},
      printerros: {
        firstname: false,
        email: false,
        contact: false,
        othernames: false,
      },
      errorlist: [],
      faculties: [],
      form: {},
      tempinput: "",
    };
  },
  methods: {
    ...mapActions({
      ex_campusfaculties: "store_campuses/ex_campusfaculties",
      exdelete: "exdelete",
      ex_profile: "ex_profile",
      ex_apiprofileupdate: "ex_apiprofileupdate",
      ex_updatecurrentname: "ex_updatecurrentname"
    }),
    navback() {
      this.$router.push({
        name: "Settings",
      });
    },
    close() {
      this.exdelete();
    },
    onSubmit(evt) {
      evt.preventDefault();
      this.form["api_id"] = 0;
      this.ex_apiprofileupdate(this.form).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Profile Settings",
          variant: response.status == 200 ? "success" : "danger",
          solid: true,
        });
        if (response.status == 200) {
          this.ex_updatecurrentname(response.data);
          this.complete = true;
          this.notifyuser = response.message;
          this.reset();
        } else {
          this.errorlist = response.data;
          this.haserror = true;
        }
      });
    },
    reset() {
      this.errorlist = [];
      this.haserror = false;
    },
    getusers() {
      this.ex_profile().then((response) => {
        this.form = response.data;
        delete this.form.api_faculty;
        this.backup = JSON.parse(JSON.stringify(this.form));
        for (const [key] of Object.entries(this.form)) {
          if (key == "api_nim") {
            break;
          } else if (key == "api_staffid") {
            this.userhas = 1;
            this.tempinput = response.data.api_staffid;
          }
        }
      });
    },
    clearerror(field) {
      this.printerros[field] = false;
      delete this.errorlist[field];
    },
    geterrors() {
      for (const [key, value] of Object.entries(this.errorlist)) {
        switch (key) {
          case "firstname":
            this.printerros.firstname = true;
            this.printerros.firstnameerror = value[0];
            break;
          case "othernames":
            this.printerros.othernames = true;
            this.printerros.othernameserror = value[0];
            break;
          case "email":
            this.printerros.email = true;
            this.printerros.emailerror = value[0];
            break;
          case "contact":
            this.printerros.contact = true;
            this.printerros.contacterror = value[0];
            break;
        }
      }
    },
  },
  computed: {
    formChanged() {
      var result = 0;
      for (const [backupkey, backupvalue] of Object.entries(this.backup)) {
        if (this.form[backupkey] == backupvalue) {
          result += 1;
        }
      }
      return result == 5 ? true : false;
    },
    errorwatcher() {
      return this.errorlist;
    },
  },
  watch: {
    errorwatcher() {
      this.geterrors();
    },
  },
  created() {
    this.getusers();
  },
};
</script>

