    ...mapActions(
      {
      apiitems: "apiitems",
      apimyborrow: "apimyborrow",
      apiusers: "apiusers",
      ex_apiupdateuser: "ex_apiupdateuser",
      ex_userBlock: "ex_userBlock",
      ex_userDelete: "ex_userDelete",
      ex_campusfaculties: "store_campuses/ex_campusfaculties",
      ex_getusers: "ex_getusers",
      ex_register: "ex_register"
    }),
    onFormsCancel() {
      this.updateform = {};
      this.createform = {}
    },
    onConfirmCancel() {
      this.confirmation = false;
    },
    formaction(type) {
            alert("Form Action")

      this.updatemode = type;
      switch (type) {
        case 1:
          // Update a user
          this.formButton = "Create";
          break;
        case 2:
          // Update user
          this.formButton = "Update";
          this.updateform = this.currentuser
          this.updateform.api_password = ""
          break;
        case 3:
          this.formButton = this.currentuser.api_status == 1 ? "Block" : "Unblock";
          this.confirmation = true;
          break;
        case 4:
          this.formButton = "Delete";
          this.confirmation = true;
          break;
      }
              console.log("Gettting Out side" + type)

      if (type == 1 || type == 2) {
        console.log("Gettting")
        this.fn_getcampuses();
      }
      this.confirmation ? this.$bvModal.show("modal-confirmation") : this.$bvModal.show("modal-forms")
    },
    form_navigator(e) {
      e.preventDefault();
      switch (this.updatemode) {
        case 1:
          // Creat a user
          this.fn_usercreate();
          break;
        case 2:
          // Update a user
                            this.preview = false;

          this.fn_userupdate();
          break;
        case 3:
                  this.preview = false;

          this.fn_userBlock();
          break;
        case 4:
                  this.preview = false;
          this.fn_deleteuser();
          break
      }
    },
    confirm_modelAction(bvModalEvt) {
      bvModalEvt.preventDefault();
      this.modalConfirmAction();
    },
    modalConfirmAction() {
      this.confirmation ? (this.$nextTick(() => {
        this.$bvModal.hide("modal-confirmation");
        this.$bvModal.show("modal-forms");
        this.confirmation = false;
      })) : this.$bvModal.show("modal-forms")
    },

    // 1 - Create a user
    fn_usercreate() {
      this.createform.api_cpassword = this.createform.api_password;
      
      
      console.log(this.createform.api_role)
      let data = new FormData();
      data.append("api_firstname", this.createform.api_firstname);
      data.append("api_othernames", this.createform.api_othernames);
      data.append("api_role", this.createform.api_role);
      data.append("api_picture", this.file);
      data.append("api_email", this.createform.api_email);
      data.append("api_nim", this.createform.api_nim);
      data.append("api_password", this.createform.api_password);
      data.append("api_cpassword", this.createform.api_cpassword);
      data.append("api_contact", this.createform.api_contact);
      data.append("api_campus", this.createform.api_campus);
      data.append("api_faculty", this.createform.api_faculty);
      data.append("api_staffid", this.createform.api_staffid);
      data.append("api_status", this.createform.api_status);

      this.ex_register(data).then((response) => {
        console.log(response)
          if (response.status == 200) {
            this.defaultcreateform();
            this.$bvToast.toast(response.message, {
              title: "Register",
              variant: response.status == 200 ? "primary" : "danger",
              solid: true,
            });
        this.$nextTick(() => {
            this.$bvModal.hide('modal-usercreate')
      })

                     this.$nextTick(() => {
            this.$bvModal.hide("modal-forms");
          });
                           this.clearview();

          }
        });

    },
    // 2 - Update a user
    fn_userupdate() {
      if(this.passwordSelector != 1){
        if(this.passwordSelector == 3){
          this.updateform.api_password = this.passwordvalue;
        }
        if(!this.updateform.api_password || this.updateform.api_password.length < 6){
        alert("Check password length ");
        this.passwordSelector = 1;
        return;
        }
      }
      let data = new FormData();
      data.append("_method", "PUT");
      data.append("api_id", this.updateform.api_id);
      data.append("api_firstname", this.updateform.api_firstname);
      data.append("api_othernames", this.updateform.api_othernames);
      data.append("api_role", this.updateform.api_role);
      data.append("api_picture", this.newPicture());
      data.append("api_email", this.updateform.api_email);
      data.append("api_nim", this.updateform.api_nim);
      data.append("api_password", this.updateform.api_password);
      data.append("api_cpassword", this.updateform.api_password);
      data.append("api_contact", this.updateform.api_contact);
      data.append("api_role", this.updateform.api_role);
      data.append("api_campus", this.updateform.api_campus);
      data.append("api_faculty", this.updateform.api_faculty);
      data.append("api_staffid", this.updateform.api_staffid);
      data.append("api_status", this.updateform.api_status);

      this.ex_apiupdateuser(data).then((response) => {
          this.$bvToast.toast(response.message, {
            title: "Update",
            variant: response.status == 200 ? "primary" : "danger",
            solid: true,
          });
        if (response.status == 200) {
          this.$nextTick(() => {
            this.$bvModal.hide("modal-forms");
          });

          this.clearview();
        }

          if(response.status == 400){
            this.updateerror = response.data
          }
      });
    
      this.clearview();
    },
    // 3 - Block a user
    fn_userBlock() {
      this.ex_userBlock(this.currentuser.api_id).then((response) => {
        this.$bvToast.toast(response.message, {
          title: this.currentuser.api_status ? "Block User " : "Unblock User",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
         this.preview = false;
        this.getusers();
      //         if (response.status == 200) {
      // this.clearview();
      //   }
      })
          },
    // 4 - Delete user function
    fn_deleteuser() {
      this.ex_userDelete(this.currentuser.api_id).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Delete User",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        if (response.status == 200) {
                  this.$nextTick(() => {
            this.$bvModal.hide('modal-usercreate')
      })

                     this.$nextTick(() => {
            this.$bvModal.hide("modal-forms");
          });

          this.currentuser = JSON.parse(JSON.stringify(this.filterusers[0]));
                    this.preview = false
        }

      });
    },
    fn_getcampuses() {
      alert("Getting")
      this.ex_campusfaculties().then((response) => {
        this.campus = response.data.filter((campus) => {
          return campus.api_name != 'admin';
        });
        console.log(this.campus[0])
        if (this.updatemode == 2) {
          this.updateform = JSON.parse(JSON.stringify(this.currentuser));
          this.updateform.api_campus = this.currentuser.api_campus.api_id;
          this.updateform.api_picture = [];
        }
        else{
          this.createform.api_campus = this.campus[0].api_id;
        }
      });
    },
getusers() {
      this.ex_getusers().then((response) => {
        this.apiuserscollection = response.data;
      });
    },

    setpreview(index) {
      this.previewreload = true;
      setTimeout(() => {
        this.preview = true;
        this.previewindex = index;
        this.currentuser = JSON.parse(JSON.stringify(this.filterusers[index]));
        this.previewreload = false;
      }, 1000);
    },

    getrole(role) {
      return role == 1 ? "Student" : role == 2 ? " Staff " : " Admin ";
    },
    geturl(url) {
      alert(url);
      let itemfetech = this.$router.resolve({
        name: "Itemdetails",
        params: {
          item: url,
        },
      });
      window.open(itemfetech.href, "_blank");
    },
    fn_cancelupdate() {
      this.show_updatemodal = false;
      this.updateform = {};
    },

    get_userheader() {
      return this.currentuser.api_role == 1 ? "NIM" : "Staff ID";
    },
    get_useridentity() {
      return this.currentuser.api_role == 1 ?
        this.currentuser.api_nim :
        this.currentuser.api_staffid;
    },

    generatepassword() {
      let password =
        Math.random().toString(36).substring(2, 6) +
        Math.random().toString(36).substring(2, 8);
      this.passwordvalue = password;
    },
    getfacultyapi() {},
    getfullname(name) {
      return name.api_firstname + " " + name.api_othernames;
    },
    fn_showcreateuser() {},
    getcampusfaculty(user) {
      return user.api_faculty.api_name + " @ " + user.api_campus.api_name;
    },
    countblockedusers() {
      let active = 0;
      let blocked = 0;
      this.apiuserscollection.filter((user) => {
        if (user.api_status == 0) {
          this.blockedcount += 1;
        }
        if (user.api_status == 1) {
          active += 1;
        }
      });
      this.activecount = active;
      this.blockedcount = blocked;
    },
    fetchfaculty() {
      let getcampus = this.updatemode == 1 ? this.updateform.api_campus : this.createform.api_campus;
      let faculty = [];
      this.campus.filter(function(campus) {
        if (campus.api_id == getcampus) {
          faculty = campus.api_faculties;
        }
      });
      this.campusfaculties = faculty;
    },
    formbuttontheme() {
      switch (this.formButton) {
        case 'Delete':
          return 'danger';
        case 'Block':
          return 'danger';
        default:
          return 'success'

      }
    },
    clearview() {
      this.preview = false;
      this.previewindex = "-";
      this.currentuser = {},
      this.viewingitem = "-";
      this.passwordSelector = 1;
      this.getusers();
    },
    closepreview() {
      this.preview = false;
      this.previewindex = "-";
      this.currentuser = {},
      this.viewingitem = "-";
      this.passwordSelector = 1;
    },
