      const file = e.target.files[0];
      this.file = e.target.files[0];
      this.imagepreviewer = URL.createObjectURL(file);
      URL.revokeObjectURL(file); // free memory
    },
    clearPicture() {
      this.createform.api_picture = [];
      this.imagepreviewer = "";
      this.file = ""
    },
    getUserfaculty() {
      this.campus.forEach((campus) => {
        if(this.formButton == 'Create' && this.createform.api_campus == campus.api_id){
          this.faculty = campus.api_faculties;
        }

        if(this.formButton == 'Update' && this.updateform.api_campus == campus.api_id){
          this.faculty = campus.api_faculties;
        }
      });
    },    
    whichAction(){
        if(this.formButton == 'Create'){
          return this.createform.api_campus
        }

        if(this.formButton == 'Update'){
          return this.updateform.api_campus
        }
    },
    newPicture(){
      return this.file ? this.file : this.updateform.api_picture;
    },
    passwordtype(e){
      this.passwordSelector = e;
    },
         customsearch(){

    },
           defaultcreateform(){
         this.imagepreviewer = "";
        this.createform.api_firstname = "";
        this.createform.api_othernames = "";
        this.createform.api_role = 1;
        this.createform.api_picture = [];
