  computed: {
    ...mapGetters(["get_user"]),
    totalresult() {
      let counter = this.items.length.toString();
      return `(${counter})  ${
        this.items.length == 1 ? " item " : "items "
      }  found`;
    },
    activeitem() {
      return this.borroweditems.length > 0 ? 1 : 0;
    },
    searchtype() {
      return this.settab;
    },
    filterusers() {
      return this.apiuserscollection.filter((user) => {
        if (this.settab == 1) {
          return (
            user.api_status == 1 &&
            user.api_firstname
            .toLowerCase()
            .includes(this.filterinput.toLowerCase())
          );
        }
        return (
          !user.api_status &&
          user.api_firstname.toLowerCase().includes(this.filterinput.toLowerCase())
        );
      });
    },
    checkpassword() {
      return this.passwordSelector;
    },
    checkcampus() {
      let value = 0;
      if(this.updatemode == 1 ){
        value = this.createform.api_campus ? this.createform.api_campus : 0;
      }
      else if (this.updatemode == 2) {
        value = this.updateform.api_campus ? this.updateform.api_campus : 0;
      }
      else{
        value = 0
      }
      return value;
    },
    closeaction() {
      return this.preview;
    },
    getcounter() {
      return this.apiuserscollection;
    },
      watchtab() {
      return this.settab
    },
    activecampus() {
     return this.whichAction()
      
    },
        watchpreview() {
      return this.preview;
    },


  },
  watch: {
    searchtype() {
      this.previewreload = true;
      setTimeout(() => {
        this.previewreload = false;
        this.preview = false;
      }, 1000);
    },
    checkpassword() {
      this.updateform.api_password = "";
      this.passwordvalue = "";
    },
    checkcampus() {
        this.fetchfaculty();
    },
    getcounter() {
      this.countblockedusers();
    },
    watchtab() {
      this.preview = false;
      this.previewreload = false;
      this.currentuser = {}
      this.filterinput = ""

    },
    activecampus() {
      this.getUserfaculty();
    },
  },

  created() {
    this.getusers();
  },
};
