import axios from '@/axios'
import shared from "../shared/index"

const state = {
    loading: false,
};

// getters
const getters = {
    Loader: (store) => {
        return store.store_shared.loading
    }
};

// actions
const actions = {
    // Add an Item
    ex_addItem({ commit }, itemdata) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.post("app_items",
                itemdata,
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    }
                }
            ).then((response => {
                commit('Loader')
                resolve(response.data)
            })).catch(err => {
                commit('Loader')
                reject(err)
            })
        })
    },    // Request change password.
    ex_forgotpassword({ commit }, userdata) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.post("app_forgotpassword",
                userdata,
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    }
                }
            ).then((response => {
                commit('Loader')
                resolve(response.data)
            })).catch(err => {
                commit('Loader')
                reject(err)
            })
        })
    },
    // Update an Item
    ex_updateItem({ commit }, itemdata) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.put("app_items/" + itemdata.api_id,
                itemdata.api_data,
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    }
                }
            ).then((response => {
                console.log(response.data);
                commit('Loader')
                resolve(response.data)
            })).catch(err => {
                commit('Loader')
                reject(err)
            })
        })
    },
};

// mutations
const mutations = {
    Loader: (state) => state.loading = !state.loading
};

const modules = {
    store_shared: shared
};

export default {
    namespaced: true,
    name: 'items',
    state,
    getters,
    actions,
    mutations,
    modules
}
