<template>
  <b-container fluid>
    <div class="main-wrapper">
      <b-container>
        <h5 class="css-indicator" @click="navback">
          <b-icon icon="chevron-left"></b-icon>Messages
        </h5>
        <div class="message-wrapper app_2border">
          <div
            class="sender"
            v-for="message in messagecontent"
            :key="message.api_id"
          >
            <b-img
              v-bind="mainProps"
              rounded="circle"
              :src="message.api_sender.profile"
              alt="Circle image"
            ></b-img>
            <div class="mt-3">
                          <p class="who">{{ message.api_sender.user == get_user.userid ? 'Recipient' : 'Sender' }} : {{ message.api_sender.user == get_user.userid ? upperIt(message.api_recipient.name) : upperIt(message.api_sender.name) }}</p>
            <p class="mt-3">Subject : Booking ID - {{ message.api_subject }}</p>
            </div>
            <b-list-group>
              <b-list-group-item
                v-for="(thread, threadindex) in message.api_message"
                :key="threadindex"
                :class="{ activethread: showindex == threadindex + 1 }"
              >
                <div class="figures" @click="showcontent(threadindex + 1)">
                  <span class="header">
                    {{ subject(message.api_subject, threadindex + 1) }}
                    {{ message.api_subject }}</span
                  >
                  <b-img
                    rounded="circle"
                    v-bind="thumbnail"
                    :src="message.api_sender.profile"
                    alt="Circle image"
                  ></b-img>
                </div>
                <div
                  class="threadcontent"
                  :class="{ showthread: showindex == threadindex + 1 }"
                >
                  {{ thread.content }}
                  <div class="replypane app_2border" v-if="reply">
                    <b-form-textarea
                      v-model="form.message"
                      id="textarea-auto-height"
                      placeholder="Message center"
                      rows="3"
                      max-rows="8"
                      :disabled="message.api_close == 1"
                    >
                    </b-form-textarea>
                  </div>
                </div>
              </b-list-group-item>
            </b-list-group>
  <div class="mt-5"> 
                <template v-if="reply">
              <div class="control">
                <b-button variant="danger" class="" @click="replycontext"
                  >Cancel</b-button
                >
                <b-button @click="sendmessage">Send Message</b-button>
              </div>
            </template>
            <template v-else>
              <div class="control">
                <b-button
                  class="outline-success"
                  :class="{ css_disabled: message.api_close == 1 }"
                  :disabled="message.api_close == 1"
                  @click="reply = true"
                  >{{ message.api_close == 1 ? "Closed" : "Reply" }}</b-button
                >
                <b-button
                  v-if="get_user.role != 1"
                  @click="closethread"
                  class="close-thread"
                  :variant="
                    message.api_close == 1 ? 'outline-secondary' : 'danger'
                  "
                  :disabled="
                    message.api_close == 1 && get_user.role != 3
                  "
                  >{{ messagestatus(message) }}</b-button
                >
              </div>
            </template>
  </div>
          </div>
        </div>
      </b-container>
    </div>
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "app-message",
  props: ["propscontent"],
  data: () => ({
    showindex: 0,
    allindex: 0,
    reply: false,
    mainProps: { width: 50, height: 50, class: "m1" },
    thumbnail: { width: 20, height: 20, class: "m1" },
    messagecontent: [],
    fields: ["no", "sender", "subject", "status"],
    form: {
      message: "",
      email: "",
    },
  }),
  methods: {
    ...mapActions(["apiamessage", "apisendmessage", "ex_closethread"]),
    getmessages() {
      this.apiamessage(this.propscontent).then((response) => {
        this.messagecontent = response.data;
        this.getindex();
      });
    },
    navback() {
      this.$router.push({
        name: "Message",
      });
    },
    getindex() {
      let count = 0;
      this.messagecontent.forEach((thread) => {
        thread.api_message.forEach(() => {
          count += 1;
        });
      });
      this.showindex = count;
      this.allindex = count;
    },
    showcontent(index) {
      this.showindex = index;
    },
    replycontext() {
      this.showindex = this.allindex;
      this.reply = false;
    },
    subject(subject, count) {
      return "Re:".repeat(count) + subject;
    },

    sendmessage() {
      if (!this.propscontent) {
        return;
      }


      this.form.email = this.propscontent;
      this.apisendmessage(this.form).then((response) => {
        console.log(response)
        if (response.status) {
          this.$bvToast.toast("Message successfully sent", {
            title: "Message",
            variant: "success",
            solid: true,
          });
        }
      });
      this.clear();
      this.getmessages();
    },
    closethread() {
      this.ex_closethread({ thread: this.propscontent }).then((response) => {
        if (response.status) {
          this.$bvToast.toast(response.message, {
            title: "Message",
            variant: response.status == 200 ? "success" : "danger",
            solid: true,
          });
          this.getmessages();
        }
      });
    },
    messagestatus(message) {
      let init;
      switch (this.get_user.role) {
        case 2:
          init = message.api_close == 1 ? "Closed" : "Close Conversation";
          break;
        case 3:
          init = message.api_close == 1 ? "Open Again?" : "Close Conversation";
          break;
        default:
          init = "Closed Conversation";
          break;
      }
      return init;
    },
    clear() {
      this.form.message = "";
      this.form.email = "";
      this.reply = false;
    },
    upperIt(name){
      return name[0].toUpperCase() + name.substring(1);
    }
  },
  computed: {
    ...mapGetters(["get_user"]),
  },
  created() {
    this.getmessages();
  },
};
</script>

