import axios from '@/axios'
import shared from "../shared/index"

const state = {
    loading: false,
};

// getters
const getters = {
    Loader: (store) => {
        return store.store_shared.loading
    }
};
// actions
const actions = {
    apiitems({ commit }) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.get("app_items",
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    }
                }
            ).then((response => {
                commit('Loader')
                resolve(response.data)
            })).catch(err => {
                commit('Loader')
                reject(err)
            })
        })
    },
    apigetitem({ commit }, item) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.get("app_items/" + item,
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    }
                }
            ).then((response => {
                commit('Loader')
                resolve(response.data)
            })).catch(err => {
                commit('Loader')
                reject(err)
            })
        })
    },
    // Add an Item - Done
    ex_addItem({ commit }, itemdata) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.post("app_items",
                itemdata,
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    }
                }
            ).then((response => {
                commit('Loader')
                resolve(response.data)
            })).catch(err => {
                commit('Loader')
                reject(err)
            })
        })
    },
    // Update an Item
    ex_updateItem({ commit }, itemdata) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.post("app_items/" + itemdata.get('api_id'), itemdata,
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    }
                }
            ).then((response => {
                commit('Loader')
                resolve(response.data)
            })).catch(err => {
                commit('Loader')
                reject(err)
            })
        })
    },
    ex_setavailable({ commit }, item) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.post("app_setitemavailable", { api_id: item }, {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                }
            }).then((response => {
                commit('Loader')
                resolve(response.data);
            })).catch(err => {
                commit('Loader')
                reject(err)
            });
        })
    },
    ex_deleteitem({ commit }, deleteitem) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.delete("app_items/" + deleteitem, {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                }
            }).then((response => {
                commit('Loader')
                resolve(response.data);
            })).catch(err => {
                commit('Loader')
                reject(err)
            });
        })
    }
};

// mutations
const mutations = {
    Loader: (state) => state.loading = !state.loading
};

const modules = {
    store_shared: shared
};

export default {
    namespaced: true,
    name: 'items',
    state,
    getters,
    actions,
    mutations,
    modules
}
