<script>
import { mapActions } from "vuex";

export default {
  name: "app-message",
  data() {
    return {
      status: "0",
      password: "",
      form: {
        email: "",
        name: "",
        food: null,
        checked: [],
      },
      foods: [
        { text: "Select One", value: null },
        "JWC",
        "Anggrek Campus",
        "Alam Sutra",
        "Bekasi",
        "Bandung",
      ],
      show: true,
    };
  },
  methods: {
    ...mapActions(["exdelete"]),
    navback() {
      this.$router.push({
        name: "Settings",
      });
    },
    close() {
      this.exdelete();
    },
    onSubmit(evt) {
      evt.preventDefault();
      alert(JSON.stringify(this.form));
    },
    onReset(evt) {
      evt.preventDefault();
      // Reset our form values
      this.form.email = "";
      this.form.name = "";
      this.form.food = null;
      this.form.checked = [];
      // Trick to reset/clear native browser form validation state
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    },
  },
  computed: {},
  created() {},
};
</script>

