<template>
  <b-container fluid>
    <div class="app_main_wrapper">
      <b-container>
        <div class="currenSection">
          <h5
            class="css-indicator-nav"
            style="padding-left: 20px; padding-right: 40px"
            @click="navback"
          >
            <b-icon icon="chevron-left"></b-icon>Home
          </h5>
          <h5 class="css-indicator-active">Forgot Password</h5>
        </div>
        <div class="form-parent app_rounded app_topmargin">
          <template v-if="complete">
            <success-notification></success-notification>
            <hr />
            <h3>{{ notifyuser }}</h3>
          </template>
          <template v-else>
            <h3 class="app_pageheader app_20padding app_bottommargin">
              <b-icon icon="person-fill" aria-hidden="true"></b-icon> Request a change of password.
            </h3>
            <b-form @submit="onSubmit">
              <b-form-group id="input-group-1" label-for="input-1">
                <b-form-input
                  id="input-1"
                  v-model="form.api_email"
                  type="text"
                  required
                  placeholder="Enter email"
                  @change="clearmessage"
                ></b-form-input>
              </b-form-group>
              <b-form-group
                id="input-group-5"
                label-for="input-5"
                v-if="haserror"
              >
                  <b-list-group-item
                    class="app_roundedlist app_notifyerror d-flex justify-content-between align-items-center"
                    ><b-badge class="emailbadge" variant="danger" pill >{{form.api_email}}</b-badge>
                    <span class="app_listchildright">{{
                      message
                    }}</span></b-list-group-item
                  >
              </b-form-group>
              <b-button
                class="app_button app_topmargin"
                type="submit"
                variant="primary"
                >Request</b-button
              >
            </b-form>
          </template>
        </div>
      </b-container>
    </div>
  </b-container>
</template>

<script>
import { mapActions } from "vuex";
import SuccessNotification from "../Success";
export default {
  name: "app-message",
  components: {
    SuccessNotification,
  },
  data() {
    return {
      complete: false,
      status: "0",
      haserror: false,
      userhas: 0,
      notifyuser: "",
      password: "",
      backup: {},
      message: "",
      faculties: [],
      form: {},
      tempinput: "",
    };
  },
  methods: {
    ...mapActions({
      ex_forgotpassword: "store_users/ex_forgotpassword",
    }),
    navback() {
      this.$router.push({
        name: "Access",
      });
    },
    close() {
      this.exdelete();
    },
    onSubmit(evt) {
      evt.preventDefault();
      this.ex_forgotpassword(this.form).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Profile Settings",
          variant: response.status == 200 ? "success" : "danger",
          solid: true,
        });
        if (response.status == 200) {
          this.complete = true;
          this.notifyuser = response.message;
        } else {
          this.message = response.message;
          this.haserror = true;
        }
      });
    },
    clearmessage(){
      if(this.haserror){
        this.haserror = false;
      }
    }
  },
  computed: {
 
  }
};
</script>

