import Vue from 'vue'
import VueRouter from 'vue-router'
import Access from '@/components/MainIndexes/AccessIndex.vue'
// import Home from '@/components/Views/ActivityViews/HomeView.vue'
import Dashboard from '@/components/MainIndexes/DashboardIndex.vue'
import ItemDetail from '@/components/Views/ActivityViews/ItemdetailsView.vue'
import Booking from '@/components/Views/ActivityViews/Booking.vue'
import MessageDetail from '@/components/Views/ActivityViews/MessagesView.vue'
import MessageViewReader from '@/components/Views/ActivityViews/MessageViewReader.vue'
import SettingsDetail from '@/components/Views/ActivityViews/SettingsView.vue'
import SettingsProfile from '@/components/Views/ActivityViews/SettingsComponents/ProfileSettings.vue'
import PictureSettings from '@/components/Views/ActivityViews/SettingsComponents/PictureSettings.vue'
import SettingsPassword from '@/components/Views/ActivityViews/SettingsComponents/PasswordSettings.vue'
import SettingsAccount from '@/components/Views/ActivityViews/SettingsComponents/AccountSettings.vue'
import InitStaff from '@/components/Views/ActivityViews/StaffComponents/Init.vue'
import ForgotPassword from '@/components/Views/ActivityViews/PasswordComponents/ForgotPassword.vue'
import ResetPassword from '@/components/Views/ActivityViews/PasswordComponents/ResetPassword.vue'
import SearchCollection from '@/components/Views/Collections/ItemsCollection.vue'
import BorrowingsCollection from '@/components/Views/Collections/BorrowingsCollection.vue'
import BorrowingCollection from '@/components/Views/Collections/BorrowingCollection.vue'
import ActiveCollection from '@/components/Views/Collections/ActiveCollection.vue'
import UsersCollection from '@/components/Views/Collections/UsersCollection.vue'
import UserCollection from '@/components/Views/Collections/UserCollection.vue'
import CampusesCollection from '@/components/Views/Collections/CampusesCollection.vue'
import PageNotFound from '@/components/MainIndexes/NotFound.vue'
import store from '../store'




Vue.use(VueRouter)
const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Access',
      component: Access,
      meta: { preventIfAuth: true }

    },
    {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard,
      meta: { requiresAuth: true }
    },
    {
      path: '/manageitems',
      name: 'GetSearch',
      component: SearchCollection,
      meta: { requiresAuth: true }
    },
    {
      path: '/activeborrowings',
      name: 'GetActiveBorrowings',
      component: ActiveCollection,
      meta: { requiresAuth: true }
    }, {
      path: '/manageborrowings',
      name: 'GetBorrowings',
      component: BorrowingsCollection,
      meta: { requiresAuth: true }
    }, {
      path: '/manageborrowing',
      name: 'GetBorrowing',
      component: BorrowingCollection,
      meta: { requiresAuth: true }
    }, {
      path: '/manageusers',
      name: 'GetUser',
      component: UsersCollection,
      meta: { requiresAuth: true }
    }, {
      path: '/manageuser',
      name: 'GetUsers',
      component: UserCollection,
      meta: { requiresAuth: true }
    }, {
      path: '/managecampuses',
      name: 'GetCampuses',
      component: CampusesCollection,
      meta: { requiresAuth: true }
    },
    {
      path: '/message',
      name: 'Message',
      component: MessageDetail,
      meta: { requiresAuth: true }
    }, {
      path: '/message/:propscontent',
      name: 'MessageContent',
      component: MessageViewReader,
      props: true,
      meta: { requiresAuth: true }
    }, {
      path: '/items/:propscontent',
      name: 'Itemdetails',
      component: ItemDetail,
      props: true,
      meta: { requiresAuth: true }

    }, {
      path: '/items/:item/book',
      name: 'Booking',
      component: Booking,
      props: true,
      meta: { requiresAuth: true }
    }, {
      path: '/settings',
      name: 'Settings',
      component: SettingsDetail,
      meta: { requiresAuth: true }
    }, {
      path: '/settings/profile',
      name: 'SettingsProfile',
      component: SettingsProfile,
      meta: { requiresAuth: true }
    }, {
      path: '/settings/picture',
      name: 'SettingsPicture',
      component: PictureSettings,
      meta: { requiresAuth: true }
    }, {
      path: '/settings/password',
      name: 'SettingsPassword',
      component: SettingsPassword,
      meta: { requiresAuth: true }
    }, {
      path: '/settings/account',
      name: 'SettingsAccount',
      component: SettingsAccount,
      meta: { requiresAuth: true }
    }, {
      path: '/staff/init',
      name: 'StaffInit',
      component: InitStaff,
      meta: { requiresAuth: true }
    }, {
      path: '/forgotpassword',
      name: 'ForgotPassword',
      component: ForgotPassword,
      meta: { preventIfAuth: true }
    }, {
      path: '/resetpassword',
      name: 'ResetPassword',
      component: ResetPassword,
      props: true,
      meta: { preventIfAuth: true, requiresAuth: true }
    },
    { path: "*", component: PageNotFound }
  ]
})

router.beforeEach((to, from, next) => {
  store.dispatch('checkstatus').then((active) => {
            if(active){
                if (to.matched.some(record => record.meta.requiresAuth)) {
                    if (!active) {
                        next({name: "Access"})
                    }
                      else{
                        next()}
                    }
                                else if (to.matched.some(record => record.meta.preventIfAuth) && active) {
              next('/dashboard');
             }
              else {next()}
            }
            else {next()}

          })

    }
)

export default router
