<template>
  <b-container fluid class="parent">
    <div class="main-wrapper">
      <template v-if="closed">
        <b-container class="bg-white">
          <success-notify></success-notify>
          <p>{{ notifyuser }}</p>
          <b-button @click="goto('Access')" class="mt-8" variant="danger">
            close
          </b-button>
        </b-container>
      </template>
      <template v-else>
        <b-container>
          <div class="currenSection">
            <h5 class="css-indicator" @click="goto('Settings')">
              <b-icon icon="chevron-left"></b-icon>Back
            </h5>
            <h5 class="css-indicator">Delete Account</h5>
          </div>
          <div class="form-parent">
            <template v-if="activeitems">
              <h3 class="borrowed-message">{{ notifyuser }}</h3>
              <br />
              <div class="listborrowings">
                <b-list-group class="mt-6">
                  <b-list-group-item
                    href="#"
                    class="d-flex justify-content-evenly align-items-start"
                    v-for="item in activeitems"
                    :key="item.api_id"
                  >
                    <b-avatar
                      :src="item.api_item.api_thumbnail"
                      size="6rem"
                    ></b-avatar>
                    <div class="pl-4">
                      <div
                        class="text-left flex-column w-100 justify-content-between align-items-start"
                      >
                        <h5 class="mb-1 capit">{{ item.api_item.api_name }}</h5>
                        <p class="mb-1">
                          Borrow Date : {{ item.api_borrowdate }}
                        </p>
                        <p class="mb-1">
                          Return Date : {{ item.api_returndate }}
                        </p>
                      </div>
                    </div>
                  </b-list-group-item>
                </b-list-group>
              </div>
            </template>
            <template v-else>
               <b-form @submit.prevent.once="close">
              <div class="message-wrapper">
                  <b-form-group
                    id="input-group-1"
                    label-for="input-1"
                    description="Provide the correct password"
                  ></b-form-group>
                  <b-form-input
                    id="input-1"
                    v-model="password"
                    type="password"
                    required
                    placeholder="Enter password"
                  ></b-form-input>
               
              </div>

              <div v-if="valideerror">
                <div class="errasemessage">
                  <p>{{notifyuser}}</p>
                </div>
              </div>

              <div class="erase">
                <div class="errasemessage">
                  <p>You will no longer have access to this application</p>
                  <b-form-checkbox
                    id="checkbox-1"
                    v-model="status"
                    name="checkbox-1"
                    value="1"
                    unchecked-value="0"
                  >
                    I accept the terms and use
                  </b-form-checkbox>
                </div>
                <b-button
                  class="btn btn-danger css-delbtn"
                  type="submit"
                  @click="close"
                  :disabled="!haspassword"
                  >Close Account</b-button
                >
              </div>
               </b-form>
            </template>
          </div>
        </b-container>
      </template>
    </div>
  </b-container>
</template>

<script>
import { mapActions } from "vuex";
import SuccessNotify from "../Success";
export default {
  name: "app-message",
  components: {
    SuccessNotify,
  },
  data() {
    return {
      status: "0",
      closed: false,
      password: "",
      notifyuser: "",
      activeitems: "",
      valideerror: false,
    };
  },
  methods: {
    ...mapActions(["ex_unregister", "ex_validatepassword"]),
    goto(link) {
      this.$router.push({
        name: link,
      });
    },

    close(evt) {
      evt.preventDefault();
      this.ex_validatepassword(this.password).then((authresponse) => {
        if (authresponse.status == 200) {
          this.ex_unregister().then((response) => {
            this.notifyuser = response.message;
            this.$bvToast.toast(this.notifyuser, {
              title: "Close Account",
              variant: response.status == 200 ? "success" : "danger",
              solid: true,
            });
            if (response.status == 200) {
              this.closed = true;
              setTimeout(() => {
                this.goto("Access");
              }, 5000);
            }
            if (response.status == 400) {
              this.activeitems = response.data;
            }
          });
        }
        if (authresponse.status == 401) {
          this.valideerror = true;
        }
        this.notifyuser = authresponse.message;

      });
    },
  },
  computed: {
    haspassword() {
      return this.password.length > 0 && this.status == 1 ? true : false;
    },
  },
};
</script>

