<template>
  <div>
    <b-navbar toggleable="lg" class="main-navbar" type="dark" variant="default">
      <b-navbar-brand href="#">
        <b-img> </b-img>
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto">
          <b-nav-item :class="{ activenav: navindex == 1 }" @click="setnav(1)"
            ><b-icon icon="inbox-fill"></b-icon> Dashboard</b-nav-item
          >
          <b-nav-item :class="{ activenav: navindex == 2 }" @click="setnav(2)"
            ><b-icon icon="inbox-fill"></b-icon> Message</b-nav-item
          >
          <b-nav-item-dropdown right class="owner">
            <!-- Using 'button-content' slot -->
            <template v-slot:button-content >
              <b-icon icon="person-fill"></b-icon> {{get_user.name}}
            </template>
            <b-dropdown-item @click="setnav(3)">Settings</b-dropdown-item>
            <b-dropdown-item @click="logout">Sign Out</b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "router-index",
  data() {
    return {
      navindex: "-",
    };
  },
  methods: {
    ...mapActions(["exlogout"]),
    logout() {
      this.exlogout().then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Logged Out",
          variant: response.status == 200 ? "success" : "danger",
          solid: true,
        });
        if (response.status == 200) {
          this.$router.push({
            name: "Access",
          }).catch(() => {});
        }
      });
    },

    setnav(nav) {
      if (this.navindex == nav) {
        return;
      }
      this.navindex = nav;

      if (nav == 1) {
        this.$router.push({
          name: "Dashboard",
        });
      }
      if (nav == 2) {
        this.$router.push({
          name: "Message",
        });
      }
      if (nav == 3) {
        this.$router.push({
          name: "Settings",
        });
      }
    },
  },
  computed: {
    ...mapGetters(["get_usersession", 'get_user']),
    watchnavlink(){
      return this.$route.path;
    },
  },
  watch: {
    watchnavlink(val){
      let link = val.slice(1).toString();
      switch (link) {
        case 'dashboard':
          this.navindex = 1;
          break;
        case 'message':
          this.navindex = 2;
          break;
        case 'settings':
          this.navindex = 3;
          break;
        default:
          this.navindex = "-"
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.main-navbar {
  background-image: linear-gradient(to right, #1446c6 ,  #277fed);
}

.activenav {
  border-radius: 30px;
  background: rgba(0, 0, 0, 0.26);
}
.nav-link {
  margin: 0 4px;
}
.owner{
  border-radius: 30px;
  background: rgba(0, 0, 0, 100);
}
</style>
