        api_name: "",
        api_description: "",
        api_thumbnail: [],
        api_quantity: 1,
        api_available: 1,
        api_campus: "",
        api_faculty: "",
        api_videos: [],
      },
      title: "",
      formimage: {},
      sourceinput: "",
      errorImageDimension: false,
      campuses: [],
      apiitemcollections: [],
      faculty: [],
