    setPreview(index) {
      this.previewreload = true;
      this.viewingitem = index;
      setTimeout(() => {
        this.preview = index;
        this.currentview = this.filterItems[index];
        this.previewreload = false;
      }, 1000);
    },

    formaction(type) {
      this.updatemode = type;
      switch (type) {
        case 1:
          this.formButton = "create";
          this.getcampuses();
          break;
        case 2:
          this.formButton = "Update";
          this.updateform = JSON.parse(JSON.stringify(this.currentview));
          this.updateform.api_thumbnail = [];
          this.preview = false;
          break;
        case 3:
          this.formButton = "Block";
          this.confirmation = true;
          break;
        case 4:
          this.formButton = "Delete";
          this.confirmation = true;
          break;
      }
      this.confirmation ?
        this.$bvModal.show("modal-confirmation") :
        this.$bvModal.show("modal-forms");
    },
    form_navigator(e) {
      e.preventDefault();
      switch (this.updatemode) {
        case 1:
          // Create an item.
          this.fn_create();
          break;
        case 2:
          // Update an item
          this.fn_update();
          break;
        case 3:
          // Block
          this.fn_block();
          break;
        case 4:
          // Delete item
          this.fn_delete();
          break;
      }
    },
    confirm_modelAction(bvModalEvt) {
      bvModalEvt.preventDefault();
      this.modalConfirmAction();
    },
    modalConfirmAction() {
      this.confirmation ?
        this.$nextTick(() => {
          this.$bvModal.hide("modal-confirmation");
          this.$bvModal.show("modal-forms");
          this.confirmation = false;
        }) :
        this.$bvModal.show("modal-forms");
    },
    onFormsCancel() {
      this.updateform = {};
      this.createform = {};
    },
    onConfirmCancel() {
      this.confirmation = false;
    },

    // 1 - Create/Add item
    fn_create() {
      let data = new FormData();
      data.append("api_name", this.createform.api_name);
      data.append("api_thumbnail", this.file);
      data.append("api_quantity", this.createform.api_quantity);
      data.append("api_available", this.createform.api_available);
      data.append("api_campus", this.createform.api_campus);
      data.append("api_faculty", this.createform.api_faculty);
      data.append("api_videos", JSON.stringify(this.createform.api_videos));
      data.append("api_description", this.createform.api_description);
      this.ex_addItem(data).then((response) => {      
        this.$bvToast.toast(response.message, {
          title: "Create Item",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        if (response.status == 200) {
          this.preview = false;
          this.$nextTick(() => {
            this.$bvModal.hide("modal-forms");
          });
          this.defaultcreate();
          this.clearview();
        }
        else{
          this.createerror = response.data
        }
      });
    },
    // 2 - Update Item
    fn_update() {
      let data = new FormData();
      data.append("_method", "PUT");
      data.append("api_id", this.currentview.api_id);
      data.append("api_name", this.updateform.api_name);
      data.append("api_thumbnail",    this.currentview.api_thumbnail == this.updateform.api_thumbnail ? this.currentview.api_thumbnail :
        this.file
      );
      data.append("api_quantity", this.updateform.api_quantity);
      data.append("api_available", this.updateform.api_available);
      data.append("api_campus", this.updateform.api_campus);
      data.append("api_faculty", this.updateform.api_faculty);
      data.append("api_campusinfo", this.updateform.api_campusinfo);
      data.append("api_videos", JSON.stringify(this.updateform.api_videos));
      data.append("api_description", this.updateform.api_description);  
      this.ex_updateItem(data).then((response) => {
        if (response.status == 200) {
                          this.preview = false;

          this.$bvToast.toast(response.message, {
            title: "Update Item",
            variant: response.status == 200 ? "primary" : "danger",
            solid: true,
          });
                    this.$nextTick(() => {
            this.$bvModal.hide("modal-forms");
          });
                    this.clearview();
         }
      // else{
      //   this.$nextTick(() => {
      //     this.$bvModal.hide("modal-forms");
      //   });
      // }
    }
      );
    },
    //3 - Block Item
    fn_block() {
      this.ex_setavailable(this.currentview.api_id).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Block Item",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        if (response.status == 200) {
                          this.preview = false;

                    this.$nextTick(() => {
            this.$bvModal.hide("modal-forms");
          });

                    this.clearview();
         }
      });
    },
    // 4 - Delete an Item
    fn_delete() {
      this.ex_deleteitem(this.currentview.api_id).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Delete Item",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
              if (response.status == 200) {
                                this.preview = false;

                    this.$nextTick(() => {
            this.$bvModal.hide("modal-forms");
          });

                    this.clearview();
         }
        
      });

    },
    clearview() {
      this.currentview = {};
      this.viewingitem = "-"
      this.getItems();
    },

    browse() {
      this.query = true;
    },

    // Go to Item details
    gotoitem(getid) {
      let itemfetech = this.$router.resolve({
        name: "Itemdetails",
        params: {
          propscontent: getid.api_id,
        },
      });
      window.open(itemfetech.href, "_blank");
    },

    // Get campuses...
    getcampuses() {
      this.ex_campusfaculties().then((response) => {
        this.campuses = response.data.filter((campus)=>{
          return campus.api_name != 'admin';
        });
      });
    },

    // Get and upload faculity on campus selection
    getfaculty() {
      if(this.updatemode == 1){
        this.campuses.forEach((campus) => {
        if (this.createform.api_campus == campus.api_id) {
          this.faculty = campus.api_faculties;
        }
      });
      }
      else{
        this.campuses.forEach((campus) => {
        if (this.updateform.api_campus.api_id == campus.api_id) {
          this.faculty = campus.api_faculties;
        }
      });
      }
    },
    uploadImage(e) {
      const file = e.target.files[0];
      this.file = e.target.files[0];
      this.imagepreviewer = URL.createObjectURL(file);
      URL.revokeObjectURL(file); // free memory
    },
    upload() {
      this.ex_uploaditem(this.itemform).then((response) => {
        console.log(response);
      });
      // this.$validator.validateAll().then((result) => {
      //   // if(!result || (_.isEmpty(this.formimage.name))) return;
      // })
    },
    addsource() {
      if (this.updatemode == 1) {
        if (
          this.sourceinput.length < 1 ||
          this.createform.api_videos.length > 3
        ) {
          return;
        }
        this.createform.api_videos.push({
          id: this.createform.api_videos.length + 1,
          src: this.sourceinput,
        });
      } else {
        if (
          this.sourceinput.length < 1 ||
          this.updateform.api_videos.length > 3
        ) {
          return;
        }
        this.updateform.api_videos.push({
          id: this.updateform.api_videos.length + 1,
          src:this.sourceinput,
        });
      }
      this.sourceinput = "";
    },
    // Get all items
    getItems() {
      this.apiitems().then((response) => {
        this.apiitemcollections = response.data;
      });
    },
    clearPicture() {
      this.createform.api_thumbnail = [];
      this.imagepreviewer = "";
    },
    closepreview() {
      this.preview = false;
      this.viewingitem = "-";
    },
    videoplayer(link){
      return "https://www.youtube.com/embed/" + link;
    },
    defaultcreate(){
        this.imagepreviewer = "";
        this.createform.api_name = "";
        this.createform.api_description = "";
        this.createform.api_thumbnail = [];
        this.createform.api_quantity = 1;
        this.createform.api_available = 1;
        this.createform.api_campus = "";
        this.createform.api_faculty = "";
        this.createform.api_videos = [];
      }
  },
  computed: {
    ...mapGetters(["get_user"]),
    filterItems() {
      let all = this.filterinput.length > 0 ? this.apiitemcollections : this.apiitemcollections.slice((this.currentPage - 1) * this.perPage, this.currentPage * this.perPage)
      return all.filter((item) => {
        return (item.api_name.toLowerCase().includes(this.filterinput.toLowerCase()));
      })
    },
    totalresult() {
      let counter = this.items.length.toString();
      return `(${counter})  ${
        this.items.length == 1 ? " item " : "items "
      }  found`;
    },
    activecampus() {
      return this.updatemode == 1 ?
        this.createform.api_campus :
        this.updateform.api_campus;
    },
    renderVideos() {
      return this.updatemode == 1 ?
        this.createform.api_videos :
        this.updateform.api_videos;
    },
    watchpreview() {
      return this.preview;
    },
    themebutton() {
      return this.formButton == "Delete" ? "danger" : "success";
    },
  },
  watch: {
    activecampus() {
      this.getfaculty();
    },
  },
  created() {
    this.getItems();
  },
};
</script>

