import axios from '@/axios'
import shared from "../shared/index"

const state = {
    loading: false,
};

// getters
const getters = {
    Loader: (store) => {
        return store.store_shared.loading
    }
};

// actions
const actions = {
    // Get borrowings
    ex_getborrowings({ commit }) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.get("app_borrow",
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    }
                }
            ).then((response => {
                commit('Loader')
                resolve(response.data)
            })).catch(err => {
                commit('Loader')
                reject(err)
            })
        })
    },   // Get borrowings
    ex_getownborrowings({ commit }) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.get("app_ownborrowings",
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    }
                }
            ).then((response => {
                commit('Loader')
                resolve(response.data)
            })).catch(err => {
                commit('Loader')
                reject(err)
            })
        })
    },
    // Create Borrowing
    ex_createborrowing({ commit }, borrowingdata) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.post("app_borrow",
                borrowingdata,
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    }
                }
            ).then((response => {
                commit('Loader')
                resolve(response.data)
            })).catch(err => {
                commit('Loader')
                reject(err)
            })
        })
    },
    // Update an Item
    ex_updateborrowing({ commit }, borrowingdata) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.put("app_borrow/" + borrowingdata.api_id,
                borrowingdata.api_data,
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    }
                }
            ).then((response => {
                commit('Loader')
                resolve(response.data)
            })).catch(err => {
                commit('Loader')
                reject(err)
            })
        })
    },

    // Approve borrowing
    ex_approveborrowing({ commit }, requestdata ) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.post("app_borrowservice", requestdata , {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                }
            }).then((response => {
                commit('Loader')
                resolve(response.data);
            })).catch(err => {
                commit('Loader')
                reject(err)
            });
        })
    },  


      // Delete borrowing
    ex_deleteborrowing({ commit }, deleteborrowing) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.delete("app_borrow/" + deleteborrowing, {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                }
            }).then((response => {
                commit('Loader')
                resolve(response.data);
            })).catch(err => {
                commit('Loader')
                reject(err)
            });
        })
    },

    apiborrow({ commit }, itemdata) {
        commit('Loader')
        return new Promise((resolve, reject) => {
            axios.post("app_borrow",
                itemdata,
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    }
                }
            ).then((response => {
                commit('Loader')
                resolve(response.data)
            })).catch(err => {
                commit('Loader')
                reject(err)
            })
        })
    },
};

// mutations
const mutations = {
    Loader: (state) => state.loading = !state.loading
};

const modules = {
    store_shared: shared
};

export default {
    namespaced: true,
    name: 'items',
    state,
    getters,
    actions,
    mutations,
    modules
}
