<script>
import {
  mapGetters,
  mapActions
} from "vuex";
import EmptyContent from "./EmptyViews/EmptyContent.vue";
import EmptyPreview from "./EmptyViews/EmptyPreview.vue";
export default {
  components: {
    EmptyContent,
    EmptyPreview,
  },
  methods: {
    ...mapActions({
      apimyborrow: "apimyborrow",
      ex_getborrowings: "store_borrowings/ex_getborrowings",
      ex_getownborrowings: "store_borrowings/ex_getownborrowings",
      ex_create: "store_borrowings/ex_createborrowing",
      ex_update: "store_borrowings/ex_updateborrowing",
      ex_delete: "store_borrowings/ex_deleteborrowing",
      ex_approve: "store_borrowings/ex_approveborrowing",
      ex_postnewmessage: "ex_postnewmessage",
      ex_getusers: "ex_getusers",
      apiitems: "store_items/apiitems",
    }),
    sendMessage() {
      let messagedata = {
        recipient: this.apiborrowings[this.preview].borrower.id,
        subject: this.apiborrowings[this.preview].id +
          "-" +
          this.apiborrowings[this.preview].item.name,
        message: this.messageform,
      };
      this.ex_postnewmessage(messagedata).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Message",
          variant: response.status == 1 ? "primary" : "danger",
          solid: true,
        });
      });
    },
    tabAction(tab) {
      tab == 1 ?
        (this.settab = 1) :
        (this.settab = 2);
    },
    setactive(set) {
      this.paneservice = set;
    },

    print() {
// Get HTML to print from element
const prtHtml = document.getElementById('activecontent').innerHTML;

// Get all stylesheets HTML
let stylesHtml = '';
for (const node of [...document.querySelectorAll('link[rel="stylesheet"], style')]) {
  stylesHtml += node.outerHTML;
}

// Open the print window
const WinPrint = window.open('', '', 'left=0,top=0,width=800,height=900,toolbar=0,scrollbars=0,status=0');

WinPrint.document.write(`<!DOCTYPE html>
<html>
  <head>
    ${stylesHtml}
  </head>
  <body>
    ${prtHtml}
  </body>
</html>`);

WinPrint.document.close();
// WinPrint.close();

WinPrint.focus();
WinPrint.print();

    },
    gotoitem(getid) {
      let itemfetech = this.$router.resolve({
        name: "Itemdetails",
        params: {
          item: getid,
        },
      });
      window.open(itemfetech.href, "_blank");
    },
    setpreview(index) {
      this.previewreload = true;
      this.viewingitem = index;
      setTimeout(() => {
        this.preview = index;
        this.currentborrowing = this.filterborrowing[index];
        this.previewreload = false;
      }, 1000);
    },
    geturl(url) {
      alert(url);
    },
    formaction(type) {
      this.updatemode = type;
      switch (type) {
        case 1:
          this.getusers();
          break;
        case 2:
          this.formButton = "Update";
          this.updateform = JSON.parse(JSON.stringify(this.currentborrowing));
          break;
        case 3:
          this.formButton = "Approve";
          break;
        case 4:
          this.formButton = "Reject";
          break;
        case 5:
          this.formButton = "Return";
          break;
        case 6:
          this.formButton = "Delete";
          break;
      }
      this.$bvModal.show("modal-forms");
    },
    form_navigator() {
      switch (this.updatemode) {
        case 1:
          this.fn_create();
          break;
        case 2:
          this.fn_update();
          break;
        case 3:
          this.fn_approvexreturn();
          break;
        case 4:
          this.fn_reject();
          break;
        case 5:
          this.fn_approvexreturn();
          break;
        case 6:
          this.fn_delete();
          break;
      }
    },
    // Create Borrowing
    fn_create() {
      this.ex_create(this.createform).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Create Borrowing",
          variant: response.status == 1 ? "primary" : "danger",
          solid: true,
        });
        this.clearview();
      });
    },
    // Update borrowing
    fn_update() {
      let data = {
        api_id: this.currentborrowing.api_id,
        api_data: this.updateform,
      };
      this.ex_update(data).then((response) => {
        this.$bvToast.toast(response.message, {
          title: "Update Borrowing",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        this.clearview();
      });
    },
    // 3 - 5 Approve or return
    fn_approvexreturn() {
      this.ex_approve(this.currentborrowing.api_id).then((response) => {
        this.$bvToast.toast(response.message, {
          title: this.updatemode == 3 ? "Approve" : "Return",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        this.clearview();
      });
    },
    // 6 - Delete borrowing
    fn_delete() {
      this.ex_delete(this.currentborrowing.api_id).then((response) => {
        this.$bvToast.toast(response.message, {
          title: this.updatemode == 3 ? "Approve" : "Return",
          variant: response.status == 200 ? "primary" : "danger",
          solid: true,
        });
        this.clearview();
      });
    },
    getusers() {
      this.ex_getusers().then((response) => {
        this.formButton = "Create";
        this.userscollections = response.data;
      });
      this.apiitems().then((response) => {
        this.itemscollections = response.data;
      });
    },
    getborrowings() {
      this.ex_getownborrowings().then((response) => {
        this.apiborrowings = response.data;
        this.activeonly = this.apiborrowings.filter((borrowing) => {
          return (borrowing.api_request.status != 2)
        });
        this.completed = this.apiborrowings.filter((borrowing) => {
          return (borrowing.api_request.status == 2)
        });
      });

    },
    clearview() {
      this.preview = false;
      this.getborrowings();
    },
    thisbookingstatus(booking) {
      switch (booking.status) {
        case 0:
          return "Pending";
        case 1:
          return "Approved";
        case 2:
          return "Completed";
        default:
          return "N/A";
      }
    },
    thisapprovedby() {
      return this.currentborrowing.api_request.approvedby ?
        this.currentborrowing.api_request.approvedby :
        "N/A";
    },
    customsearch() {
      let result = this.apiborrowings.filter((borrowing) => {
        return (borrowing.api_id == this.filterinput)
      });
      if (result.length > 0) {
        this.sample = result;
        this.pendingborrowing = result;
        this.$bvToast.toast('Borrowing found', {
          title: "Approve",
          variant: "primary",
          solid: true,
        });
      } else {
        this.searchempty = true;
      }
    },
    closepreview() {
      this.preview = false;
      this.viewingitem = "-";
    },
  },
  data() {
    return {
      activeonly: [],
      completed: [],
      usercode: "",
      searchempty: false,
      viewingitem: "-",
      currentPage: 1,
      perPage: 30,
      apiborrowings: [],
      currentborrowing: {},
      settab: 1,
      updatemode: 0,
      formButton: "",
      filterinput: "",
      userscollections: [],
      itemscollections: [],
      preview: false,
      previewreload: false,
      query: false,
      messageform: "",
      borrowingform: {},
      createform: {},
      updateform: {},
      campus: [],
      search: "",
      borrowingcode: "",
      itemscollection: [],
      borroweditems: [],
      pendingborrowing: {},
      sample: {},
      variant: "dark",
      paneservice: 1,
      mainProps: {
        width: 50,
        height: 50,
        class: "m1"
      },
    };
  },
  computed: {
    ...mapGetters(["get_user"]),
    totalresult() {
      let counter = this.items.length.toString();
      return `(${counter})  ${
        this.items.length == 1 ? " item " : "items "
      }  found`;
    },
    filterborrowing() {
      let all = (this.settab == 1  ? this.activeonly : this.completed).slice((this.currentPage - 1) * this.perPage, this.currentPage * this.perPage)
      return all.filter((borrowing) => {
        return (borrowing.api_borrower.api_name.toLowerCase().includes(this.filterinput.toLowerCase()) || borrowing.api_item.api_name.toLowerCase().includes(this.filterinput.toLowerCase()));
      })
    },
    checkinput() {
      return this.pendingborrowing;
    },
    watchtab() {
      return this.settab
    }
  },
  watch: {
    checkinput() {
      this.currentborrowing = this.pendingborrowing[0]
    },
    watchtab() {
      this.preview = false;
      this.currentborrowing = {}
      this.filterinput = ""
      this.pendingborrowing = {}
    }
  },
  created() {
    this.getborrowings();
  },
};
</script>

